<template>
    <div class="dashboard-calculator-viewer">
        <div class="flex-layout flex-row flex-wrap">
            <h4 class="dashboard-calc-editor-title">
                <div style="font-size: 50%;">Calculator</div>
                <span v-if="displayInfo">{{calculator.name}}</span>
                <span v-else>{{paramCalculatorName || '...'}}</span>
            </h4>
            <div class="flex-1"></div>

            <template v-if="displayInfo">
                <button @click="gotoEditCalculator" type="button" class="btn">
                    <v-icon icon="edit"></v-icon>
                    Edit Calculator
                </button>

                <button 
                    @click="showDeleteDialog" 
                    style="margin-left: 8px;"
                    type="button" class="btn btn-error btn-auto-width">
                    <v-icon icon="trash-2"></v-icon>
                </button>
            </template>
        </div>

        <template v-if="displayInfo">
            <div class="card">
                <div class="card-header">
                    <div class="card-title">General</div>
                </div>

                <div class="card-body">
                    <div class="info-group">
                        <div class="info-name">Calculator URLs</div>
                        <div class="info-value">
                            <a :href="calculatorLink" target="_blank">
                                Click Here To View Your Calculator
                                <sup>
                                    <v-icon icon="external-link"></v-icon>
                                </sup>
                            </a>
                            <div style="margin-top: 8px;">
                                <button
                                    @click="showLinkGeneratorModal = true"
                                    type="button" class="btn btn-sm">
                                    <v-icon icon="link-2"></v-icon>
                                    Generate URLs &amp; Code
                                </button>
                            </div>
                        </div>
                    </div>
                    <div class="info-group">
                        <div class="info-name">Last Modified</div>
                        <div class="info-value">{{formatDateTime(calculator.modified)}}</div>
                    </div>

                    <div class="info-group">
                        <div class="info-name">Created</div>
                        <div class="info-value">{{formatDateTime(calculator.created)}}</div>
                    </div>
                </div>
            </div>

            <div class="card" style="margin-top: 32px;">
                <div class="card-header">
                    <div class="card-title">Reports</div>

                    <div class="card-body">
                        <report-wrapper :calculatorUUID="calculator.uuid"></report-wrapper>
                    </div>
                </div>
            </div>
        </template>

        <div v-if="loading"
            class="flex-1 flex-layout flex-center flex-center-cross"
            style="align-self: center;">
            <v-spinner size="64px"></v-spinner>
        </div>

        <div v-if="errorText"
            class="flex-1 flex-layout flex-center flex-center-cross"
            style="align-self: center;">
            <div class="label error">
                {{errorText}}
            </div>
        </div>

        <!-- Delete Modal -->
        <v-modal
            v-if="displayInfo"
            :small="true"
            :active="deleteDialogActive"
            :xbutton="false">
            <div class="modal-title" slot="header">Delete Calculator</div>
            <div class="content" slot="body">
                <div class="delete-modal-content">
                    If you would like to delete this calculator type <span class="label">{{calculator.name}}</span>
                    into the box below and click <span class="label error">Delete</span>.
                </div>

                <div style="margin-top: 8px;">
                    <input 
                        :disabled="isDeleting"
                        placeholder="Calculator Name"
                        class="form-input" type="text" v-model="deleteCalculatorConfirmText">
                </div>
            </div>
            <div slot="footer">
                <button @click="deleteDialogActive = false" :disabled="isDeleting" type="button" class="btn">
                    Cancel
                </button>
                <button @click="deleteCalculator"
                    :disabled="isDeleting || !deleteCalculatorConfirmTextGood"
                    type="button" class="btn btn-flex btn-error">
                    <v-icon icon="trash-2" v-if="!isDeleting"></v-icon>
                    <v-spinner icolor v-if="isDeleting" style="margin-right: 8px;" size="1em"></v-spinner>
                    {{ isDeleting ? 'Deleting' : 'Delete' }}
                </button>
            </div>
        </v-modal>

        <!-- Link Generation Modal -->
        <v-modal
            v-if="displayInfo"
            :fill-small-screen="true"
            :active="showLinkGeneratorModal"
            @xclick="showLinkGeneratorModal = false"
            @shadow-click="showLinkGeneratorModal = false">

            <div class="modal-title" slot="header">
                Generate Links &amp; Code
            </div>

            <div class="content" slot="body">
                <form>
                    <div class="form-group">
                        <label class="form-label">Generate</label>

                        <label class="form-radio">
                            <input v-model="linkgen.type" value="url" type="radio" name="linkGenerateType" />
                            <i class="form-icon"></i> URL
                        </label>

                        <label class="form-radio">
                            <input v-model="linkgen.type" value="iframe" type="radio" name="linkGenerateType" />
                            <i class="form-icon"></i> IFrame
                        </label>
                    </div>

                    <div class="form-group">
                        <label class="form-label">Options</label>

                        <label class="form-checkbox">
                            <input type="checkbox" v-model="linkgen.hideHeader" />
                            <i class="form-icon"></i> Hide Header
                        </label><br />

                        <label class="form-checkbox">
                            <input type="checkbox" :disabled="linkgen.hideHeader" v-model="linkgen.hideLogo" />
                            <i class="form-icon"></i> Hide Logo
                        </label>
                    </div>

                    <div class="form-group">
                        <label class="form-label">Generated Link / Code</label>
                        <div class="link-output-area">
                            {{generatedCode}}
                        </div>
                    </div>
                </form>
            </div>
        </v-modal>
    </div>
</template>

<script>
import {createToast} from '../../misc/toast';
import * as API from '../../misc/api';

import VIcon from '../frag/v-icon';
import VSpinner from '../frag/v-spinner';
import VModal from '../frag/v-modal';
import ReportWrapper from '../frag/report-wrapper';

import CalculatorMixin from '../../mixins/CalculatorMixin';
import DateMixin from '../../mixins/DateMixin';

const DEL_WHITESPACE_REGEX = new RegExp('\\s+', 'g');

export default {
    mixins: [CalculatorMixin, DateMixin],

    data: () => ({
        calculator: null,
        loading: false,
        errorText: null,

        paramCalculatorSID: null,
        paramCalculatorName: null,

        showLinkGeneratorModal: false,
        linkgen: {
            type: 'url',
            hideHeader: false,
            hideLogo: false
        },

        
        isDeleting: false,
        deleteCalculatorConfirmText: null,
        deleteDialogActive: false
    }),

    watch: {
        '$route': 'fetchCalculatorData'
    },

    created() {
        this.fetchCalculatorData();
    },

    computed: {
        deleteCalculatorConfirmTextGood() {
            if (this.calculator && this.deleteCalculatorConfirmText) {
                DEL_WHITESPACE_REGEX.lastIndex = 0;
                const name = this.calculator.name ? 
                    this.calculator.name.replace(DEL_WHITESPACE_REGEX, '') : '';

                DEL_WHITESPACE_REGEX.lastIndex = 0;
                const test = this.deleteCalculatorConfirmText.replace(DEL_WHITESPACE_REGEX, '');

                return name.toLowerCase() == test.toLowerCase();
            } else {
                return false;
            }
        },

        generatedCode() {
            const url = this.generatorCalculatorLink({
                hideHeader: this.linkgen.hideHeader,
                hideLogo: this.linkgen.hideLogo
            });
            switch (this.linkgen.type) {
                case 'iframe':
                    return `<iframe src="${url}"></iframe>`;
                default: // 'url'
                    return url;
            }
        },

        calculatorLink() {
            if (this.calculator) {
                return `${process.env.SERVER_ORIGIN}/calc/${this.calculator.shortid}`;
            }
        },

        displayInfo() { return this.calculator && !this.loading; }
    },

    methods: {
        gotoEditCalculator() {
            this.$router.push({
                name: 'calculator-edit',
                query: { mode: 'edit', sid: this.calculator.shortid }
            });
        },

        generatorCalculatorLink(options) {
            const hideHeader = !!options['hideHeader'];
            const hideLogo = !!options['hideLogo'];
            
            let param;
            if (hideHeader) {
                param = 'hbrand';
            } else if (hideLogo) {
                param = 'hbrandlogo';
            } else {
                param = '';
            }
            
            return this.calculatorLink + (param ? '?' + param : '');
        },

        async fetchCalculatorData() {
            // Just clearing some variables.
            this.showLinkGeneratorModal = false;
            this.deleteDialogActive = false;
            this.isDeleting = false;
            this.errorText = null;

            if (this.$route.query) {
                this.paramCalculatorSID = this.$route.query['sid'];
                this.paramCalculatorName = this.$route.query['cname'];

                this.loading = true;
                if (this.paramCalculatorSID) {
                    try {
                        const {calculator} = await API.graph(`
                            query getCalculator($calcsid: String!) {
                                calculator(shortid: $calcsid) {
                                    uuid
                                    shortid
                                    name
                                    modified
                                    created
                                    metadata {
                                        scrapeTerms
                                        termsPageURL
                                        termInterests {
                                            rate
                                            len
                                        }
                                        defaultLoanAmount
                                        minLoanAmount
                                        maxLoanAmount
                                        minDownPayment
                                        defaultDownPayment
                                        minTermLength
                                        defaultTermLength
                                        maxTermLength
                                        minInterestRate
                                        defaultInterestRate
                                        maxInterestRate
                                        calculatorType
                                        visPrincipalVisible
                                        visInterestVisible
                                        loanAmountName
                                        downPaymentName
                                    }
                                }
                            }
                        `, {
                            calcsid: this.paramCalculatorSID
                        });
                        this.calculator = calculator;
                    } catch(e) {
                        console.error('Error while loading calculator. ', e);
                        this.errorText = 'There was an error while loading the calculator.';
                    }
                } else {
                    this.errorText = "Oh no! There is no calculator to load."
                }
                this.loading = false;
            }
        },

        showDeleteDialog() {
            this.deleteCalculatorConfirmTextGood = null;
            this.deleteDialogActive = true;
        },

        async deleteCalculator() {
            if (this.deleteCalculatorConfirmTextGood) { 
                this.isDeleting = true;
                try {
                    const {deleted} = await API.graph(`
                        mutation delCalculator($calcuuid: UUID!) {
                            deleted: deleteCalculator(uuid: $calcuuid)
                        }
                    `, { calcuuid: this.calculator.uuid });
                    if (deleted) {
                        createToast({
                            text: `Deleted calculator ${this.calculator.name}`,
                            type: 'success'
                        });
                        this.$router.replace({ name: 'calculator-list' });
                    } else {
                        createToast({
                            text: `Failed to delete calculator ${this.calculator.name}`,
                            type: 'error'
                        });
                    }
                } catch(e) {
                    console.error('Error while deleting calculator: ', e);
                    createToast({
                        text: `Failed to delete calculator ${this.calculator.name}`,
                        type: 'error'
                    });
                }
                this.isDeleting = false;
            }
        }
    },

    components: {
        VIcon,
        VSpinner,
        VModal,
        ReportWrapper
    }
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.dashboard-calculator-viewer {
    display: flex;
    padding: $layout-padding;
    align-items: stretch;
    flex-direction: column;
    overflow-y: auto;
    flex: 1 1 auto;
}

.link-output-area {
    padding: $layout-padding;
    border-left: 4px solid $border-color;
    background-color: $bg-color-dark;
}

// This needs a special line height because of the labels.
.delete-modal-content {
    line-height: $font-size-default + px2rem(3) + px2rem(8);
}

.info-group {
    display: flex;
    flex-direction: column;

    &:not(:last-child) {
        margin-bottom: $layout-padding;
    }

    .info-name {
        display: block;
        font-size: 90%;
        color: $default-text-muted-color;
    }

    .info-value {
        display: block;
    }
}
</style>



