<template lang="pug">
.facebook-conn-container
    v-loader(v-if="loading || errorText", :loading="loading", :errorText="errorText", loadingText="Loading Facebook Info")
    template(v-else)
        div(v-if="loggedInToFacebook", style="margin-bottom: 1em;")
            v-icon.success-color(icon="check-circle", style="margin-right: 1em;")
            | Connected to Facebook
        div(v-if="facebookConnected")
            div.flex-layout.flex-wrap.flex-row(style="margin-bottom: 16px;")
                v-facebook-chip(:key="id" v-for="id in facebook.ids", :fbid="id" @ondelete="removeTabFromPage")
            button.btn.btn-facebook.btn-flex.flex-center-cross(@click="installFacebookTab", style="height: auto;")
                span Install Page Tabs
        div.flex-layout.flex-column.flex-align-start(v-else)
            button.btn.btn-facebook.btn-flex.flex-center-cross(v-if="!loggedInToFacebook", @click="loginToFacebook", style="height: auto;")
                img(src="/sdash/static/img/FB-f-Logo__blue_29.png", style="margin-top: 2px; margin-bottom: 2px; margin-right: 8px;")
                span Login to Facebook
            button.btn.btn-facebook.btn-flex.flex-center-cross(v-if="loggedInToFacebook", @click="installFacebookTab", style="height: auto;")
                span Install Page Tabs
</template>

<script>
import VLoader from '../../frag/v-loader';
import VIcon from '../../frag/v-icon';
import VFacebookChip from '../../frag/v-facebook-chip';
import * as API from '../../../misc/api';
import http from '../../../misc/http';
import {createToast} from '../../../misc/toast';
import {facebookCheckLogin, facebookLogin, checkFacebookAvailable} from '../../../misc/social';

export default {
    props: ['client'],

    data: () => ({
        loading: false,
        errorText: null,
        facebook: null,
        loggedInToFacebook: false,
        facebookAccountInfo: false,
    }),

    watch: {
        'client': 'onInitialized'
    },

    created() {
        this.onInitialized();
    },

    computed: {
        facebookConnectUrl() {
            const origin = process.env.SERVER_ORIGIN;
            const appid = process.env.FACEBOOK_APP_ID;
            const redirecturi = encodeURIComponent(`${origin}/fbtabconnected/${this.client.uuid}`);
            return `https://facebook.com/dialog/pagetab?app_id=${appid}&redirect_uri=${redirecturi}`;
        },

        facebookConnected() {
            return this.loggedInToFacebook && !!(this.facebook && this.facebook.connected);
        }
    },

    methods: {
        async onInitialized() {
            await checkFacebookAvailable();
            this.loading = true;
            const fbLoggedIn = await facebookCheckLogin();
            if (fbLoggedIn) {
                this.loggedInToFacebook = true;
                this.facebookAccountInfo = fbLoggedIn;
            }

            this.loadFBInfo();
        },

        async loginToFacebook() {
            this.loading = true;
            const fbLoggedIn = await facebookLogin();
            if (fbLoggedIn) {
                this.loggedInToFacebook = true;
                this.facebookAccountInfo = fbLoggedIn;
            } else {
                createToast({
                    text: 'Unable to login to Facebook.',
                    type: 'error'
                })
            }
            this.loading = false;
        },

        async installFacebookTab() {
            await facebookLogin();
            window.location.href = this.facebookConnectUrl;
        },

        async removeTabFromPage({pageName, pageId}) {
            if (!confirm(`Are you sure you would like to delete the FinCalc tab from the page "${pageName}"?`)) return;

            try {
                await this.removeTabFromFacebookPage();
            } catch(e) {
                console.error('Facebook error', e);
                createToast({
                    htmlText: "FinCalc does not have the permissions necessary to remove the tab. Try following <a href=\"https://www.facebook.com/help/371634529571035/\">these instructions</a>",
                    delay: Infinity,
                    type: 'default'
                })
                // createToast({
                //     text: 'There was an error deleting this tab from the Facebook page.',
                //     type: 'error'
                // });
            }

            try {
                const result = await http.post(`${process.env.SERVER_ORIGIN}/fbtabremove/${this.client.uuid}/${pageId}`);
                console.log('DEL RESULT: ', result);
            } catch(e) {
                console.error('API error', e);
                createToast({
                    text: 'There was an error deleting removing page from FinCalc.',
                    type: 'error'
                });
            }

            if (this.facebook && this.facebook.ids) {
                const index = this.facebook.ids.indexOf(pageId);
                if (index >= 0) {
                    this.facebook.ids.splice(index, 1);
                }
            }
        },

        removeTabFromFacebookPage(pageId) {
            return new Promise((resolve, reject) => {
                FB.api(
                    `/${pageId}/tabs`, "DELETE", { tab: 'app_' + process.env.FACEBOOK_APP_ID },
                    function (response) {
                        if (response && !response.error) {
                            resolve(response);
                        } else {
                            reject(response['error'] || response);
                        }
                    }
                );
            });
        },

        async loadFBInfo() {
            this.loading = true;
            try {
                const data = await API.graph(`query getFacebookInfo {
                    me {
                        client {
                            facebook {
                                ids
                                connected
                            }
                        }
                    }
                }`);
                this.facebook = data.me.client.facebook;
            } catch(e) {
                console.error('Error loading Facebook information.');
                createToast({ text: 'Error loading Facebook information.', type: 'error' });
                this.errorText = 'There was an error loading your Facebook information.'
            }
            this.loading = false;
        }
    },

    components: {
        VIcon,
        VLoader,
        VFacebookChip
    }
}
</script>

<style lang="scss" scoped>
@import "~@/assets/sass/variables";
$facebook-color: #3b5998;

.success-color { color: $success-color; }

.facebook-conn-container {
    display: flex;
    flex-direction: column
}

.btn.btn-facebook {
    background-color: $facebook-color;
    color: $text-on-primary-color;
    border: 1px solid darken($facebook-color, 5%);
    &:hover, &.selected {
        background-color: darken($facebook-color, 2%);
    }
}
</style>



