let FACEBOOK_AVAILABLE_QUEUE = [];
let FACEBOOK_IS_AVAILABLE = false;

function __flushFacebookWaitQueue() {
    FACEBOOK_AVAILABLE_QUEUE.forEach(([resolve, reject]) => { resolve(); });
    FACEBOOK_AVAILABLE_QUEUE = null;
}

export function checkFacebookAvailable() {
    return new Promise((resolve, reject) => {
        if (FACEBOOK_IS_AVAILABLE) {
            resolve();
        } else {
            FACEBOOK_AVAILABLE_QUEUE.push([resolve, reject]);
        }
    })
}

export function facebookCheckLogin() {
    return new Promise((resolve, reject) => {
        try {
            FB.getLoginStatus(function(response) {
                console.log('status: ', response);
                if (response && response['error']) {
                    reject(response['error']);
                } else if (response.status === 'connected') {
                    resolve({ response });
                } else {
                    resolve(false);
                }
            });
        } catch(e) {
            reject(e);
        }
    });  
}

export function facebookLogout() {
    return new Promise((resolve, reject) => {
        try {
            FB.logout((response) => {
                if (response && response['error']) {
                    reject(response['error']);
                } else {
                    resolve(true);
                }
            });
        } catch(e) {
            reject(e);
        }
    });
}

export function facebookLogin() {
    return new Promise((resolve, reject) => {
        try {
            FB.getLoginStatus(function(response) {
                if (response && response['error']) {
                    reject(response['error']);
                } else if (response.status === 'connected') {
                    resolve({
                        response
                    });
                } else {
                    FB.login((response) => {
                        if (response && response['error']) {
                            reject(response['error']);
                        } else {
                            resolve({
                                response
                            });
                        }
                    }, {scope: 'pages_show_list,public_profile'});
                }
            });
        } catch(e) {
            reject(e);
        }
    });
}

/**
 * Initializes the facebook api.
 */
export function facebookInit() {
    window.fbAsyncInit = function() {
        FB.init({
            appId            : process.env.FACEBOOK_APP_ID,
            autoLogAppEvents : true,
            xfbml            : true,
            version          : 'v2.9'
        });
        FB.AppEvents.logPageView();

        __flushFacebookWaitQueue();
        FACEBOOK_IS_AVAILABLE = true;
    };

    (function(d, s, id){
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) {return;}
        js = d.createElement(s); js.id = id;
        js.src = "//connect.facebook.net/en_US/sdk.js";
        fjs.parentNode.insertBefore(js, fjs);
    }(document, 'script', 'facebook-jssdk'));

    console.log('Facebook initialized.');
}

export function initSocialMediaPlatforms() {
    facebookInit();
}


