<template>
    <!-- #TODO: Change inputs to money & percent inputs -->
    <div class="dashboard-calculators-editor">
        <h4>
            Calculators <small class="label">{{newMode ? 'Create' : 'Edit'}}</small>
        </h4>

        <div v-if="errorText" class="flex-layout flex-column flex-1 flex-center flex-center-cross">
            <div class="label error">
                {{errorText}}
            </div>
        </div>

        <div v-if="loading && !errorText" class="flex-layout flex-column flex-1 flex-center flex-center-cross">
            <v-spinner size="64px" style="margin-bottom: 16px;"></v-spinner>
            <span v-if="newMode">Loading Editor</span>
            <span v-else>Loading Calculator</span>
        </div>

        <div v-show="!loading && !errorText" class="container flex-1">
            <div class="row dce-height-fix">
                <div class="col-6 col-sm-12">
                    <form onsubmit="return false;">
                        <div class="form-group">
                            <label class="form-label">Name</label>
                            <input v-model="calculatorName" class="form-input" type="text" placeholder="name">
                        </div>

                        <div class="form-group">
                            <label class="form-label">Calculator Type</label>
                            <select class="form-select" v-model="cdata.calculatorType">
                                <option disabled value="">Select Calculator Type</option>
                                <option :key="ctype" v-for="ctype in calculatorTypes" :value="ctype.value">
                                    {{ctype.label}}
                                </option>
                            </select>
                        </div>

                        <div class="form-group">
                            <div class="card">
                                <div class="card-header">
                                    <div class="card-title">
                                        Loan Amount
                                    </div>
                                </div>
                                <div class="card-body">
                                    <div class="form-group">
                                        <label class="form-label">Default</label>
                                        <div class="form-line">
                                            <div class="form-static">$</div>
                                            <input v-model="cdata.defaultLoanAmount" class="form-input" type="text" placeholder="Default (optional)">
                                        </div>
                                    </div>

                                    <div class="form-group">
                                        <label class="form-label">Range</label>
                                        <div class="form-line">
                                            <div class="form-static">$</div>
                                            <input v-model="cdata.minLoanAmount" class="form-input" type="text" placeholder="Minimum">
                                            <div class="form-static">To</div>
                                            <input v-model="cdata.maxLoanAmount" class="form-input" type="text" placeholder="Maximum">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="card">
                                <div class="card-header">
                                    <div class="card-title">
                                        Down Payment
                                    </div>
                                </div>
                                <div class="card-body">
                                    <div class="form-group">
                                        <label class="form-label">Default</label>
                                        <div class="form-line">
                                            <div class="form-static">$</div>
                                            <input v-model="cdata.defaultDownPayment" class="form-input" type="text" placeholder="Default (optional)">
                                        </div>
                                    </div>

                                    <div class="form-group">
                                        <label class="form-label">Minimum</label>
                                        <div class="form-line">
                                            <div class="form-static">$</div>
                                            <input v-model="cdata.minDownPayment" class="form-input" type="text" placeholder="Minimum">
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="card">
                                <div class="card-header">
                                    <div class="card-title">Loan Terms &amp; Interest Rates</div>
                                </div>
                                <div class="card-body">
                                    <div class="form-group">
                                        <label class="form-switch">
                                            <input v-model="cdata.scrapeTerms" type="checkbox" id="scrapeLoanTermsId">
                                            <i class="form-icon"></i> Scrape From Website
                                        </label>
                                    </div>
                                    <div v-if="cdata.scrapeTerms" class="form-line">
                                        <div class="form-static">Scrape From</div>
                                        <input v-model="cdata.termsPageURL" class="form-input" type="text" placeholder="http://scrape-from-here.com">
                                    </div>
                                    <div v-show="!cdata.scrapeTerms">
                                        <loan-term-editor v-model="cdata.termInterests"></loan-term-editor>
                                        <div class="flex-layout flex-center">
                                            <button @click="addTerm" class="btn" type="text">
                                                <v-icon icon="plus-circle"></v-icon>
                                                Add Term
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="card">
                                <div class="card-header">
                                    <div class="card-title">Application</div>
                                </div>
                                <div class="card-body">
                                    <div class="form-group">
                                        <label class="form-label">Application Name</label>
                                        <input v-model="cdata.applyRedirectText"
                                            class="form-input" type="text"
                                            :placeholder="applyRedirectTextPlaceholder">
                                    </div>

                                    <div class="form-group">
                                        <label class="form-label">Application URL</label>
                                        <input v-model="cdata.applyRedirect" class="form-input" type="text" placeholder="Application URL">
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <label class="form-switch">
                                <input v-model="showExtras" type="checkbox" id="scrapeLoanTermsId">
                                <i class="form-icon"></i> Extra Options
                            </label>
                        </div>

                        <div v-show="showExtras" class="form-group">
                            <div class="form-group">
                                <div class="card">
                                    <div class="card-header">
                                        <div class="card-title">
                                            Interest Rate Extras
                                        </div>
                                    </div>
                                    <div class="card-body">
                                        <div class="form-group">
                                            <label class="form-label">Default</label>
                                            <div class="form-line">
                                                <input v-model="cdata.defaultInterestRate" class="form-input" type="text" placeholder="Default (optional)">
                                                <div class="form-static">%</div>
                                            </div>
                                        </div>

                                        <div class="form-group">
                                            <label class="form-label">Range</label>
                                            <div class="form-line">
                                                <input v-model="cdata.minInterestRate" class="form-input" type="text" placeholder="Minimum">
                                                <div class="form-static">To</div>
                                                <input v-model="cdata.maxInterestRate" class="form-input" type="text" placeholder="Maximum">
                                                <div class="form-static">%</div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>     
                        </div>

                        <div class="form-group flex-layout flex-align-right flex-margin-h">
                            <button :disabled="connectingToPreview > 0" @click="refreshPreviewClicked" class="btn" type="button">Preview</button>
                            <button :disabled="saving" class="btn primary" type="text" @click="commit">
                                {{newMode ? 'Create' : 'Save'}}
                            </button>
                        </div>
                    </form>
                </div>

                <div class="col-6 col-sm-12 dce-height-fix">
                    <div class="card preview-container dce-height-fix">
                        <div class="card-header darkened">
                            <div class="card-title">
                                <button :disabled="connectingToPreview > 0" @click ="refreshPreviewClicked" class="preview-refresh-button">
                                    <v-icon icon="rotate-cw"></v-icon>
                                </button>
                                <span>Preview</span>
                            </div>
                        </div>
                        <div class="card-body flex-layout flex-column" style="position: relative;">
                            <transition name="preview-overlay-anim">
                                <div 
                                    v-if="reloadingPreview"
                                    class="flex-layout flex-column flex-center flex-center-cross preview-overlay"
                                    style="color: white;
                                        font-size: 2rem;
                                        background-color: rgba(0, 0, 0, 0.9); 
                                        position: absolute; width: 100%; height: 100%;">
                                    <div><v-icon icon="refresh-ccw"></v-icon></div>
                                    <div>Loading Preview...</div>
                                </div>
                            </transition>
                            <iframe :class="{invisible: connectingToPreview}" ref="preview" style="flex: 1;" class="dce-height-fix" :src="calculatorPreviewURL"></iframe>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import VIcon from '../frag/v-icon';
import VSpinner from '../frag/v-spinner';
import Dropdown from '../frag/dropdown';
import LoanTermEditor from './loan-term-editor';
import * as API from '../../misc/api';
import {createCalculatorPreviewData, timeout} from '../../misc/util';

import {createToast} from '../../misc/toast';

export default {
    data: () => ({
        loading: true,
        errorText: null,

        reloadingPreview: false,
        connectingToPreview: 0,
        connectedToPreview: false,
        connectionWaiter: null,
        connectionWaitStartTime: 0,

        newMode: true,
        saving: false,
        calculatorSID: '',
        
        client: {

        },

        calculatorName: '',
        cdata: {
            // Terms & Interest Rates
            scrapeTerms: false,
            termsPageURL: '',
            termInterests: [
                {len: 36, rate: 2.35},
                {len: 48, rate: 2.60},
                {len: 60, rate: 2.60}
            ],

            defaultLoanAmount: 20000,
            minLoanAmount: 100.00,
            maxLoanAmount: 300000.00,

            minDownPayment: 0.00,
            defaultDownPayment: 3000,
            minTermLength: 1, 
            defaultTermLength: 36,
            maxTermLength: 10000,

            minInterestRate: 0.00,
            defaultInterestRate: 2.35,
            maxInterestRate: 100.00,

            calculatorType: '',
            visPrincipalVisible: true,
            visInterestVisible: true,

            loanAmountName: '',
            downPaymentName: '',
            interestRateName: '',

            // Branding Stuff:
            applyRedirectText: '',
            applyRedirect: '',
        },

        showExtras: false,

        calculatorTypes: [
            {label: "Auto Loan", value: "auto-loan"},
            {label: "Mortgate Loan", value: "mortgage-loan"},
            {label: "Personal Loan", value: "personal-loan"},
        ]
    }),

    created() {
        if (this.connectionWaiter) {
            window.removeEventListener('message', this.connectionWaiter);
            this.connectionWaiter = null;
        }

        this.connectionWaiter = (event) => {
            if (event.data['messageType'] && event.data['messageType'] === 'connected') {
                const resp = event.data['time'];
                if (resp >= this.connectionWaitStartTime) {
                    this.connectedToPreview = true;
                }
            }
        };
        window.addEventListener('message', this.connectionWaiter, false);

        this.fetchCalculatorData();
    },

    destroyed() {
        if (this.connectionWaiter) {
            window.removeEventListener('message', this.connectionWaiter);
            this.connectionWaiter = null;
        }
    },

    watch: {
        '$route': 'fetchCalculatorData'
    },

    computed: {
        applyRedirectTextPlaceholder() {
            if (this.client && typeof this.client.name === 'string') {
                const trimmed = this.client.name.trim();
                if (trimmed.length > 0) return `${trimmed} Loan Application`;
            } else {
                return 'Loan Application';
            }
        },

        calculatorPreviewURL() {
            if (process.env.NODE_ENV === 'development')
                // #TODO: remember to fix this :P
                return 'http://localhost:9080/scalc/index.html';
                // return 'https://localhost:8443/calc-preview';
            else
                return `${process.env.SERVER_ORIGIN}/calc-preview`;
        },

        calculatorAcceptedOrigin() {
            return '*';
        }
    },

    methods: {
        getDefaultTermUnits() {
            switch (this.cdata && this.cdata.calculatorType) {
                case 'mortgage-loan': return 'years';
                // case 'auto-loan': return 'months';
                // case 'personal-loan': return 'months';
                default: return 'months';
            }
        },

        addTerm() {
            this.cdata.termInterests.push({
                len: 0, rate: 0, units: this.getDefaultTermUnits()
            });
        },

        async ensurePreviewConnected() {
            if (!this.connectedToPreview) {
                await this.connectToPreview();
            }
        },

        /// Waits until it can get a connection to the preview.
        async connectToPreview() {
            const MAX_ATTEMPTS = 16;
            this.connectingToPreview += 1;
            this.connectedToPreview = false;
            let attempts = 0;
            this.connectionWaitStartTime = Date.now();
            while (!this.connectedToPreview) {
                this.messagePreview('connect', {sendTime: Date.now()});

                if (!this.connectedToPreview) {
                    await timeout(500);
                    attempts++                    
                    if (attempts > MAX_ATTEMPTS) {
                        createToast({
                            text: 'Failed to connect to the preview window.',
                            type: 'error'
                        });
                        this.connectingToPreview -= 1;
                        throw new Error('Maximum connection attempts reached.');
                    } else if(attempts > 2) {
                        console.log('Failed connection attempt #%d. Retrying...', attempts);
                    }
                }
            }
            this.connectingToPreview -= 1;
        },

        /// Sends a message to the preview
        messagePreview(type, data) {
            if (this.$refs['preview'].contentWindow) {
                const message = (typeof data === 'object') && data ? Object.assign({}, data) : {data};
                message['messageType'] = type;
                console.log('sending message: ', message);
                this.$refs['preview'].contentWindow.postMessage(message,
                    this.calculatorAcceptedOrigin);
            } else {
                console.warn('No content window found for preview.');
            }
        },

        async pushPreviewData() {
            await this.ensurePreviewConnected();
            const previewData = createCalculatorPreviewData(this.calculatorName, this.cdata, this.client);
            this.messagePreview('previewUpdate', {previewData});
        },

        async refreshPreview() {
            this.reloadingPreview = true;
            await this.ensurePreviewConnected();
            await this.pushPreviewData();
            this.messagePreview('refresh');
            await timeout(0);
            this.reloadingPreview = false;
        },

        async refreshPreviewClicked() {
            await this.refreshPreview();
            createToast({
                text: 'Refreshed Calculator Preview',
                type: 'success'
            });
        },

        async commit() {
            this.saving = true;
            try {
                if (await this.checkCalculatorFields()) {
                    if (this.newMode) { await this.createCalculator(); }
                    else { await this.updateCalculator(); }
                }
            } catch(e) {
                console.error('Error while committing calculator state.', e);
            } finally {
                this.saving = false;   
            }
        },

        async checkCalculatorFields() {
            if (!this.calculatorName || this.calculatorName.length < 3) {
                createToast({
                    text: 'Calculator name must be at least 3 characters long.',
                    type: 'warning'
                });
                return false;
            }
            return true;
        },

        async createCalculator() {
            try {
                const {created} = await API.graph(`
                    mutation newCalculator($calcName: String!, $meta: CalculatorMetaInput!) {
                        created: createCalculator(name: $calcName, metadata: $meta) {
                            uuid
                            shortid
                        }
                    }`, {
                        calcName: this.calculatorName,
                        meta: this.cdata
                    });
                this.calculatorSID = created.shortid;
                this.newMode = false;

                createToast({
                    text: 'Created new calculator ' + this.calculatorName,
                    type: 'success'
                });
            } catch(e) {
                console.error('Error while creating calculator: ', e);

                createToast({
                    text: 'There was an error while creating the calculator.',
                    type: 'error'
                });
            }
        },

        async updateCalculator() {
            try {
                const {updated} = await API.graph(`
                    mutation updateCalculator($calcsid: String!, $calcName: String!, $meta: CalculatorMetaInput!) {
                        updated: updateCalculator(shortid: $calcsid, name: $calcName, metadata: $meta)
                    }
                `, {
                    calcsid: this.calculatorSID,
                    calcName: this.calculatorName,
                    meta: this.cdata
                });
                
                createToast({
                    text: 'Updated calculator ' + this.calculatorName,
                    type: 'success'
                });
            } catch(e) {
                console.error('Error updating calculator: ', e);

                createToast({
                    text: 'There was an error while updating the calculator.',
                    type: 'error'
                });
            }
        },

        async fetchCalculatorData() {
            try {
                await this.__fetchCalculatorData();
                await this.pushPreviewData();
                await this.refreshPreview();
            } catch(e) {
                console.error('Error while loading calculator. ', e);
                if (this.newMode) {
                    this.errorText = 'There was an error while loading the interface.';
                } else {
                    this.errorText = 'There was an error while loading the calculator.';
                }
            }
        },

        async __fetchCalculatorData() {
            this.loading = true;
            if (this.$route.query) {
                this.newMode = !(this.$route.query['mode'] === 'edit');
                this.calculatorSID = this.$route.query['sid'];

                if (!this.newMode) {
                    const {calculator} = await API.graph(`
                        query getCalculator($calcsid: String!) {
                            calculator(shortid: $calcsid) {
                                uuid
                                shortid
                                name
                                metadata {
                                    scrapeTerms
                                    termsPageURL
                                    termInterests {
                                        rate
                                        len
                                        units
                                    }
                                    defaultLoanAmount
                                    minLoanAmount
                                    maxLoanAmount
                                    minDownPayment
                                    defaultDownPayment
                                    minTermLength
                                    defaultTermLength
                                    maxTermLength
                                    minInterestRate
                                    defaultInterestRate
                                    maxInterestRate
                                    calculatorType
                                    visPrincipalVisible
                                    visInterestVisible
                                    loanAmountName
                                    downPaymentName
                                    applyRedirectText
                                    applyRedirect
                                }

                                client {
                                    name
                                    brandingImageURL
                                }
                            }
                        }
                    `, {
                        calcsid: this.calculatorSID
                    });
                    this.calculatorName = calculator.name;
                    this.cdata = calculator.metadata;
                    this.client = calculator.client;
                } else {
                    const {me} = await API.graph(`
                        query {
                            me {
                                client {
                                    name
                                    brandingImageURL
                                }
                            }
                        }
                    `);

                    this.client = me['client'];
                }
            }
            this.loading = false;
        }
    },

    components: {
        VIcon,
        Dropdown,
        VSpinner,
        LoanTermEditor
    }
};
</script>

<style lang="scss">
.preview-overlay-anim-enter-active, .preview-overlay-anim-leave-active {
    transition: opacity 1s;
}

.preview-overlay-anim-enter, .preview-overlay-anim-leave-to {
    opacity: 0;
}
</style>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.dashboard-calculators-editor {
    flex: 1;
    display: flex;
    padding: $layout-padding;
    align-items: stretch;
    flex-direction: column;
    overflow-y: auto;

    .preview-refresh-button {
        border: none;
        background: none;
        padding: 0;
        margin: 0;
        outline: none;

        cursor: pointer;
        color: $default-text-color;
        &:hover { color: $oc-red-4; }
        &[disabled] { opacity: 0.5; pointer-events: none; cursor: none; }
        margin-right: 8px;
    }

    .dce-height-fix {
        height: 100%;
    }

    @media screen and (max-width: $screen-size-sm ) {
        .dce-height-fix {
            height: auto;
        }
    }

    .preview-container {
        margin: 8px;
        display: flex;
        flex-direction: column;

        @media screen and (max-width: $screen-size-sm ) {
            & {
                margin: 0;
                margin-top: 16px;
            }

            iframe {
                height: 1024px;
            }
        }

        .card-body {
            padding: 0;
            margin: 0;
            flex: 1;
        }

        iframe {
            border: none;
            width: 100%;
        }
    }
}
</style>


