<template lang="pug">
label.chip.flex-layout.flex-layout-center-cross
    template(v-if="loading")
        v-spinner(style="margin-right: 8px;", size="16px")
    template(v-else)
        img(v-if="itemImage", :src="itemImage", width="16", height="16", style="border-radius: 16px; margin-right: 8px;")
        span {{itemName}}
    button(:disabled="loading" @click="deleteClicked").btn.btn-clear
</template>

<script>
import VSpinner from './v-spinner';
import Avatar from './avatar';

export default {
    props: ["fbid"],

    data: () => ({
        // This is the ID loaded from Facebook
        itemId: null,
        itemName: null,
        itemImage: null,
        loading: false
    }),

    watch: {
        "fbid": "loadFBProfile"
    },

    created() {
        this.loadFBProfile();
    },

    methods: {
        loadDataForId(id) {
            return new Promise((resolve, reject) => {
                FB.api(
                    '/' + id,
                    'GET',
                    {"fields":"id,name,picture"},
                    function(response) {
                        if (response && response['error']) {
                            reject(response['error']);
                        } else {
                            resolve(response);
                        }
                    }
                );
            });
        },

        deleteClicked() {
            this.$emit('ondelete', {
                pageName: this.itemName,
                pageId: this.itemId
            });
        },

        async loadFBProfile() {
            this.loading = true;
            try {
                const item = await this.loadDataForId(this.fbid);
                this.itemId = item.id;
                this.itemName = item.name;
                if (item.picture && item.picture.data && item.picture.data.url) {
                    this.itemImage = item.picture.data.url;
                } else {
                    this.itemImage = null;
                }
            } catch(e) {
                console.error('Error loading facebook info: ', e);
                this.itemId = null;
                this.itemName = 'ERROR';
            }
            this.loading = false;
        }
    },

    components: {
        VSpinner,
        Avatar
    }
}
</script>

<style lang="scss" scoped>
@import "~@/assets/sass/variables";
</style>



