<template lang="pug">
div.flex-layout.flex-column
    img(:width="imgWidth" src="/sdash/static/img/fincalc-logo.png")
    slot
</template>

<script>
export default {
    props: {
        'size': {type: [String, Number], 'default': 'md'},
    },


    computed: {
        imgWidth() {
            if (typeof this.size === 'number') return this.size + 'px';
            switch(this.size) {
                case 'sm': return '64px';
                case 'md': return '128px';
                case 'lg': return '256px';
                default: return this.size;
            }
        }
    }
};
</script>


