let ToastHandler = null;

/**
 * Creates a new toast and adds it to the queue.
 * Option Examplte:
* {
*   text: 'Current Toast',
*   type: 'warning', // type: primary, default, error, success, warning
*   canClose: false,
*   delay: 600
* }
 * @param {ToastOptions} options Options for the toast.
 * @returns {Promise} Promise that resolves when the toast disappears.
 */
export function createToast(options) {
    if (!ToastHandler) {
        throw new Error('Attempted to create toast before registering a handler.');
    } else {
        return ToastHandler(options);
    }
}

export function registerToastHandler(handler) {
    ToastHandler = handler;
    return ToastHandler;
}

export function unregisterToastHandler(handler) {
    if (ToastHandler === handler) {
        ToastHandler = null;
    }
}




