import { hexAsRGBA } from '@/misc/util';
import { Line } from 'vue-chartjs';

export default Line.extend({
    props: {
        labels: {type: Array, 'default': () => []},
        datasets: { type: Array, 'default': [] }
    },

    watch: {
        labels() {
            this.fillChart();
        },

        datasets() {
            this.fillChart();
        }
    },

    mounted() {
        this.fillChart();
    },

    methods: {
        fillChart() {
            if (this._chart) {
                this._chart.destroy();
            }
            
            this.renderChart({
                labels: this.labels,
                datasets: this.datasets
            }, {
                responsive: true,
                maintainAspectRatio: true
            });
        }
    }
});

