<template>
    <div class="dashboard-overview">
        <section>
            <h4>Overview <small class="label muted-color">This Month</small></h4>
            <report-display :from="overviewReportsFrom" :to="overviewReportsTo" />
        </section>
        <section>
            <h5>Recent Leads</h5>
            <div class="overview-row">
                <leads-display :from="overviewReportsFrom" :to="overviewReportsTo" :displayed="10" />
            </div>
        </section>
    </div>
</template>

<script>
import VIcon from './frag/v-icon';
import OverviewCard from './frag/overview-card';
import VStackedChart from './frag/v-stacked-chart';
import ReportDisplay from './frag/report-display';
import LeadsDisplay from './frag/leads-display';
import { hexAsRGBA } from '@/misc/util';
import { getLeads } from '../misc/api';
import * as moment from 'moment';

export default {
    data: () => ({
    }),

    computed: {
        overviewReportsFrom() {
            return moment().startOf('month');
        },

        overviewReportsTo() {
            return moment().endOf('month');
        }
    },

    components: {
        VIcon,
        ReportDisplay,
        LeadsDisplay
    }
}
</script>

<style lang="scss">
@import '~@/assets/sass/variables';

.dashboard-overview {
    flex: 1;
    display: flex;
    padding: $layout-padding;
    align-items: stretch;
    flex-direction: column;
    overflow-y: auto;

    section {
        margin-bottom: $layout-padding;
    }
}
</style>



