<template lang="pug">
.connections-settings-container
    v-loader.flex-center.flex-center-cross(v-if="loading || errorText", :loading="loading", :errorText="errorText", loadingText="Loading Organization Info")
    template(v-if="clientAvailable")
        section
            h5 Facebook
            conn-facebook.conn-content-container(:client="client")
        section
            h5 MailChimp
            conn-mailchimp.conn-content-container(:client="client")
</template>

<script>
import ConnFacebook from './connections/conn-facebook';
import ConnMailchimp from './connections/conn-mailchimp';
import VProgress from '../frag/v-progress';
import VLoader from '../frag/v-loader';
import {createToast} from '../../misc/toast';
import * as API from '../../misc/api';

export default {
    data: () => ({
        loading: true,
        errorText: null,
        client: null
    }),

    computed: {
        clientAvailable() {
            return !this.loading && !this.errorText && this.client;
        }
    },

    watch: {
        ['$route']: 'loadClient'
    },

    created() {
        this.loadClient();
    },

    methods: {
        async loadClient() {
            this.loading = true;
            this.errorText = null;
            try {
                const data = await API.graph(`query loadClientId { me { client { uuid } } }`);
                this.client = data.me.client;
            } catch(e) {
                console.error('Error while loading client: ', e);
                this.errorText = "There was an error loading your organization's information.";
                createToast({
                    text: "There was an error while loading your organization's information.",
                    type: 'error'
                });
            }
            this.loading = false;
        }
    },

    components: {
        ConnFacebook,
        VProgress,
        VLoader,
        ConnMailchimp
    }
}
</script>

<style lang="scss" scoped>
@import "~@/assets/sass/variables";

.connections-settings-container {
    padding: $layout-padding;
}

.conn-content-container {
    margin-left: $layout-padding;
}
</style>


