/**
 * Retuns a promise that will resolve in ms milliseconds.
 * @param {Number} ms Delay until promise resolves.
 */
export function timeout(ms) {
    return new Promise((resolve, _) => setTimeout(resolve, ms));
}

export function hexAsRGBA(hex, opacity) {
    if (hex.startsWith('#')) hex = hex.substr(1);
    const hexn = parseInt(hex, 16);

    // #TODO handle format #HHH
    if (hex.length == 6) {
        const r = (hexn >> 16) & 0xff
            , g = (hexn >> 8) & 0xff
            , b = hexn & 0xff;
        const o = (typeof opacity === 'number') ? Math.min(opacity, 1.0) : '1.0';
        return `rgba(${r}, ${g}, ${b}, ${o.toFixed(3)})`;
    } else if (hex.length === 8) {
        const r = (hexn >> 24) & 0xff
            , g = (hexn >> 16) & 0xff
            , b = (hexn >> 8) & 0xff
            , a = hexn & 0xff;
        const o = a / 255;
        return `rgba(${r}, ${g}, ${b}, ${o.toFixed(3)})`;
    } else {
        throw new Error(`Cannot turn this into RGBA: ${hex}`);
    }
};

export function formatNumberWithCommas(x) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

function copyprop(source, dest, sourceProp, destProp) {
    if (destProp === undefined) { destProp = sourceProp; }
    dest[destProp] = source[sourceProp];
}

function getprop(source, prop, _default) {
    const val = source[prop];
    if (val === null || val === undefined) {
        return _default;
    } else {
        return val;
    }
}

export function isStringWithContent(str) {
    return typeof str === 'string' && str.trim().length > 0;
}

export function createCalculatorPreviewData(calculatorName, calculator, client) {
    const brandingData = {};
    const calculatorData = {};

    copyprop(calculator, brandingData, 'disclaimer');
    copyprop(calculator, brandingData, 'initialPopoverHeaderText');
    copyprop(calculator, brandingData, 'initialPopoverBodyText');

    if (!getprop(calculator, 'defaultDisclaimer', true)) {
        copyprop(calculator, brandingData, 'disclaimer');
    } else {
        // #TODO: move this somewhere more suitable.
        brandingData['disclaimer'] =
            'Annual percentage rates, terms and availability may differ based upon ' +
            'evaluation of credit. Additional loan rates and terms may be available. ' +
            'Other restrictions may apply. The figures shown are hypothetical and may ' +
            'not be applicable to your individual situation.';
    }

    if (!getprop(calculator, 'defaultInitialPopoverText', true)) {
        copyprop(calculator, brandingData, 'initialPopoverHeaderText');
        copyprop(calculator, brandingData, 'initialPopoverBodyText');
    } else {
        // #TODO: Choose these defaults based on loan type.
        brandingData['initialPopoverHeaderText'] =
            "Looking for a new ride or want to refinance your existing one?";
        brandingData['initialPopoverBodyText'] =
            "Change the parameters in the calculator " +
            "to get payment estimates based on " +
            client['name'] +
            "'s current rates. Ready to buy? Click APPLY NOW " +
            "to fill out our online loan application!";
    }
    copyprop(calculator, brandingData, 'applyRedirectText');
    copyprop(calculator, brandingData, 'applyRedirect');
    if (!isStringWithContent(brandingData['applyRedirectText'])) {
        brandingData['applyRedirectText'] = `${client['name']} Loan Application`;
    }
    brandingData['logo'] = client['brandingImageURL'];

    copyprop(calculator, calculatorData, 'defaultLoanAmount');
    copyprop(calculator, calculatorData, 'minLoanAmount');
    copyprop(calculator, calculatorData, 'maxLoanAmount');
    copyprop(calculator, calculatorData, 'minDownPayment');
    copyprop(calculator, calculatorData, 'defaultDownPayment');
    copyprop(calculator, calculatorData, 'minTermLength');
    copyprop(calculator, calculatorData, 'defaultTermLength');
    copyprop(calculator, calculatorData, 'maxTermLength');
    copyprop(calculator, calculatorData, 'minInterestRate');
    copyprop(calculator, calculatorData, 'defaultInterestRate');
    copyprop(calculator, calculatorData, 'maxInterestRate');
    copyprop(calculator, calculatorData, 'calculatorType');
    copyprop(calculator, calculatorData, 'loanAmountName');
    copyprop(calculator, calculatorData, 'downPaymentName');
    copyprop(calculator, calculatorData, 'termInterests');

    return {
        branding: brandingData,
        calculator: calculatorData
    };
}


let __advancedUpload = undefined;
export function isAdvancedUpload() {
    if (__advancedUpload !== undefined) { return __advancedUpload; }
    const div = document.createElement('div');
    __advancedUpload = (('draggable' in div) || ('ondragstart' in div && 'ondrop' in div)) && 'FormData' in window && 'FileReader' in window;
    return __advancedUpload;
}

let idCounter = 0;
export function getNewElementId() {
    return 'elemI' + (idCounter++);
}

export function addEventListenerMulti(elem, events, listener) {
    events.forEach((evt) => { elem.addEventListener(evt, listener, false); });
    return listener;
}

export function removeEventListenerMulti(elem, events, listener) {
    events.forEach((evt) => { elem.removeEventListener(evt, listener) });
    return listener;
}

const FSIZE_KB = 1024;
const FSIZE_MB = FSIZE_KB * 1024;
const FSIZE_GB = FSIZE_MB * 1024;
export function bytesAsFriendlyFileSize(bytes) {
    if (bytes >= FSIZE_GB) return parseFloat((bytes / FSIZE_GB).toFixed(2)) + 'GB';
    if (bytes >= FSIZE_MB) return parseFloat((bytes / FSIZE_MB).toFixed(2)) + 'MB';
    if (bytes >= FSIZE_KB) return parseFloat((bytes / FSIZE_KB).toFixed(2)) + 'KB';
    return parseFloat(bytes.toFixed(2)) + 'B';
}


