<template>
<table class="table table-hover">
    <thead>
        <th>Name</th>
        <th>Type</th>
        <th>Last Modified</th>
    </thead>
    <tbody>
        <tr v-if="!calculators || calculators.length < 1">
            <td colspan="5" class="empty-calculators-text">
                <div v-if="loading">
                    <v-spinner size="64px"></v-spinner>
                </div>
                <template v-else>
                    <slot name="empty"></slot>
                </template>
            </td>
        </tr>
        <tr :key="calc" v-for="(calc, index) in calculators" @click="openCalculator(calc)">
            <td>{{calc.name}}</td>
            <td>
                <span class="label primary">
                    {{formatCalculatorType(calc.calculatorType)}}
                </span>
            </td>
            <td>{{formatDateTime(calc.modified)}}</td>
        </tr>
    </tbody>
</table>
</template>

<script>
import VIcon from '../frag/v-icon';
import VSpinner from '../frag/v-spinner';
import CalculatorMixin from '../../mixins/CalculatorMixin';
import DateMixin from '../../mixins/DateMixin';

export default {
    mixins: [CalculatorMixin, DateMixin],

    props: {
        'loading': { type: Boolean, 'default': false },
        'calculators': { type: Array, 'default': () => [] }
    },

    methods: {
        openCalculator(calc) {
            this.$router.push({
                name: 'calculator-view',
                query: { 'sid': calc.shortid, 'cname': calc.name }
            });
        }
    },

    components: {
        VIcon,
        VSpinner
    }
}
</script>

<style lang="scss" scoped>
.empty-calculators-text {
    font-size: 1.6rem;
    text-align: center;
}

.calc-table-btn-extra-margin {
    margin: 16px;
}
</style>



