/**
 * Generates fake data for the overview.
 */

const VISITOR_COUNT_MIN = 100;
const VISITOR_COUNT_MAX = 500;

const ENGAGEMENT_PERCENTAGE_MIN = 5;
const ENAGEMENT_PERCENTAGE_MAX = 60;

const CONVERSION_PERCENTAGE_MIN = 20;
const CONVERSION_PERCENTAGE_MAX = 40;

const LOAN_AMOUNT_MIN = 5000;
const LOAN_AMOUNT_MAX = 100000;

const DOWNPAYMENT_PERCENTAGE_MIN = 10;
const DOWNPAYMENT_PERCENTAGE_MAX = 50;

function rand(from, to) {
    const dist = to - from;
    return Math.floor(from + dist * Math.random());
}

function generateDataForDay() {
    const visits = rand(VISITOR_COUNT_MIN, VISITOR_COUNT_MAX);
    const engagementPercentage = rand(ENGAGEMENT_PERCENTAGE_MIN, ENAGEMENT_PERCENTAGE_MAX);
    const engagement = Math.round(visits * (engagementPercentage / 100));
    const conversionPercentage = rand(CONVERSION_PERCENTAGE_MIN, CONVERSION_PERCENTAGE_MAX)
    const conversion = Math.round(engagement * (conversionPercentage / 100));
    return { visits, engagement, conversion };
}

const loanTypes = ['Auto', 'Mortgage', 'Personal'];
export function generateFakeLeads(count) {
    const faker = require('faker');
    const leads = [];
    for (let idx = 0; idx < count; idx++) {
        const loanAmount = rand(LOAN_AMOUNT_MIN, LOAN_AMOUNT_MAX);
        const downpaymentPercentage = rand(DOWNPAYMENT_PERCENTAGE_MIN, DOWNPAYMENT_PERCENTAGE_MAX);
        const downPayment = Math.round(loanAmount * (downpaymentPercentage / 100));

        leads.push({
            firstName: faker.name.firstName(),
            lastName: faker.name.lastName(),
            loanType: faker.random.arrayElement(loanTypes),
            email: faker.internet.email(),
            phoneNumber: faker.phone.phoneNumber(),
            loanAmount, downPayment
        });
    }
    return leads;
}

export function generateFakeData(days) {
    let visits = { total: 0, data: [] };
    let engagement = { total: 0, data: [] };
    let conversion = { total: 0, data: [] };

    const _labels = [];

    for (let d = 0; d < days; d++) {
        const dataForDay = generateDataForDay();
        visits.data.push(dataForDay.visits);
        visits.total += dataForDay.visits;

        engagement.total += dataForDay.engagement;
        engagement.data.push(dataForDay.engagement);

        conversion.total += dataForDay.conversion;
        conversion.data.push(dataForDay.conversion);

        _labels.push(d + 1);
    }

    return {
        _labels,
        visits, engagement, conversion
    };
}


