<template>
<div class="login-main-container">
    <div class="card login-card">
        <div class="card-header">
            <div class="card-title">
                <v-branding style="align-items: flex-end;"><h5>Login</h5></v-branding>
            </div>
        </div>
        <div class="card-body">
            <div v-if="initialLogin" class="flex-layout flex-center flex-center-cross flex-column">
                <v-spinner
                    width="32px" 
                    height="32px">
                </v-spinner>
                <span>
                    Logging In...
                </span>
            </div>
            <form v-show="!initialLogin" @submit="login">
                <div class="form-group">
                    <div class="form-line">
                        <div class="form-static">
                            <v-icon icon="user"></v-icon>
                        </div>
                        <input :disabled="loggingIn" v-model="username" type="text" placeholder="Email" class="form-input">
                    </div>
                </div>

                <div class="form-group">
                    <div class="form-line">
                        <div class="form-static">
                            <v-icon icon="lock"></v-icon>
                        </div>
                        <input :disabled="loggingIn" v-model="password" type="password" placeholder="Password" class="form-input">
                    </div>
                </div>

                <div class="error-text">{{loginError || "&nbsp;"}}</div>

                <div class="form-group">
                    <div class="flex-layout flex-center-cross flex-align-right">
                        <button
                            type="button"
                            @click="gotoRegistration"
                            :disabled="loggingIn"
                            class="btn btn-link"
                            style="margin-right: 16px;">
                            Register
                        </button>
                        <button
                            :disabled="!loginButtonEnabled || loggingIn"
                            @click="login" 
                            type="submit" 
                            class="btn btn-flex primary">
                            <v-spinner icolor v-if="loggingIn" style="margin-right: 8px;" size="1em"></v-spinner>
                            <v-icon v-if="!loggingIn" icon="log-in" style="margin-right: 8px;"></v-icon>
                            {{loggingIn ? 'Logging In' : 'Log In'}}
                        </button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</div>
</template>

<script>
import VIcon from './frag/v-icon';
import VSpinner from './frag/v-spinner';
import VBranding from './frag/v-branding';
import * as API from '../misc/api';
import {mapState} from 'vuex';

import * as moment from 'moment';

export default {
    data: () => ({
        loginError: '',
        username: '',
        password: '',
        initialLogin: false,
        loggingIn: false
    }),

    mounted() {
        this.checkStatus();
    },

    methods: {
        gotoRegistration() {
            this.$router.push('/register');
        },

        async checkStatus() {
            this.initialLogin = true;
            if (await API.checkLoginStatus()) {
                if (await this.isLoggedIn()) return;
            }
            this.initialLogin = false;
        },

        async login($event) {
            $event.preventDefault();
            this.loginError = '';
            this.loggingIn = true;
            try {
                const loginResult = await API.login(this.username, this.password);
                if (loginResult && !loginResult['error']) {
                    API.useTokenInfo(loginResult);
                    if (await this.isLoggedIn()) return;
                } else {
                    this.loginError = loginResult['message'];
                }
            } catch(e) {
                console.error('Error while loggin in: ', e);
                this.loginError = 'Unknown error while loggin in.';
            }
            this.loggingIn = false;
        },

        async isLoggedIn() {
            try {
                await this.$store.dispatch('loadProfileInfo');
                this.$store.commit('setAuthenticated');

                let redirect = this.$route.query['redirect'];
                if (redirect) { redirect = redirect.trim(); }
                if (!redirect || redirect.length < 1) { redirect = '/dash/overview'; }
                this.$router.replace(redirect);

                return true;
            } catch(e) {
                await API.clearLoginStatus();
                console.error('Error while loggin in: ', e);
                this.loginError = 'There was an error while logging you in :(';
            }
            return false;
        }
    },

    computed: {
        loginButtonEnabled() {
            return this.username && this.username.length >= 3 && this.password && this.password.length >= 5;
        },

        ...mapState(['auth'])
    },

    components: {
        VIcon,
        VSpinner,
        VBranding
    }
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.login-main-container {
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: fixed;

    display: flex;
    align-items: center;
    justify-content: center;
}

.login-card {
    min-width: 400px;

    .error-text {
        color: $error-color;
        margin-top: 8px;
        margin-bottom: 8px;
    }
}

@media screen and (max-width: $screen-size-sm ) {
    .login-card {
        width: 100%;
        height: 100%;
        min-width: 100%;
        min-height: 100%;

        border: none;
    }
}
</style>



