<template lang="pug">
div
    .container(v-if="loading && !errorText"): .row.flex-center: .col-6
        v-progress(indeterminate)
    .container(v-if="errorText"): .row.flex-center
        .label.error {{errorText}}
    template(v-if="!loading && !errorText")
        form(style="margin-bottom: 16px;").container
            form.col-6.col-md-10.col-sm-12
                .form-group
                    label.form-label Presets
                    select.form-select(v-model="selectedRange")
                        option(disabled value="") (Select Preset)
                        option(value="today") Today ({{formatDate(today)}})
                        option(:key="idx" v-for="(m, idx) in months" :value="m")
                            | {{formatMonth(m)}}
                .form-group
                    label.form-label Custom Range
                    .form-line
                        v-date-picker.form-input(v-model="displayRange.from")
                        .form-static To
                        v-date-picker.form-input(v-model="displayRange.to")
        reports-display(:calculatorUUID="calculatorUUID" :range="range")

        h5(style="margin-top: 16px;") Leads
        leads-display(:calculatorUUID="calculatorUUID" :range="range")
</template>

<script>
import ReportsDisplay from './report-display';
import LeadsDisplay from './leads-display';
import VDatePicker from './v-date-picker';
import * as moment from 'moment';
import DateMixin from '@/mixins/DateMixin';
import VProgress from './v-progress';
import * as API from '@/misc/api';
import Vue from 'vue';

export default {
    mixins: [DateMixin],
    props: ['calculatorUUID'],

    data:() => ({
        loading: false,
        errorText: '',

        months: [],
        selectedRange: 'today',

        today: moment().startOf('day'),

        displayRange: {
            from: moment().startOf('day'),
            to: moment().startOf('day')
        },

        range: {
            from: moment().startOf('day'),
            to: moment().startOf('day')
        },

        rangeLock: false,
    }),

    watch: {
        ['displayRange.to'](v) {
            this.lockRange(() => {
                this.selectedRange = '';

                let to = this.displayRange.to, from = this.displayRange.from;
                if (to && from && to.isBefore(from)) {
                    let temp = to;
                    to = from;
                    from = temp;
                }

                this.range = {
                    to: to,
                    from: from
                }
            });
        },

        ['displayRange.from'](v) {
            this.lockRange(() => {
                this.selectedRange = '';

                let to = this.displayRange.to, from = this.displayRange.from;
                if (to && from && to.isBefore(from)) {
                    let temp = to;
                    to = from;
                    from = temp;
                }

                this.range = {
                    to: to,
                    from: from
                }
            });
        },

        selectedRange(v) {
            this.lockRange(() => {
                if (!v || typeof v === 'string') {
                    this.range = {
                        from: this.today,
                        to: this.today
                    };
                } else {
                    this.range = {
                        from: v.clone().startOf('month'),
                        to: v.clone().endOf('month')
                    };
                }

                this.displayRange.from = this.range.from.clone();
                this.displayRange.to = this.range.to.clone();
            });
        }
    },

    computed: {
        isSingleDay() {
            if (this.range.from && this.range.to) {
                return this.range.from.isSame(this.range.to, 'day');
            } else {
                return true;
            }
        }
    },

    created() {
        this.fetchEventRanges();
    },

    methods: {
        lockRange(m) {
            if (this.rangeLock) return;
            this.rangeLock = true;
            try {
                return m();
            } catch(e) {
                Vue.nextTick(() => this.rangeLock = false);
                throw e;
            } finally {
                Vue.nextTick(() => this.rangeLock = false);
            }
        },

        setupRanges(from, to) {
            const m = [];

            from = from.startOf('month');
            to = to.startOf('month');

            while (to.isSameOrAfter(from)) {
                m.push(to.clone());
                to.subtract(1, 'months');
            }

            this.months = m;
        },

        async fetchEventRanges() {
            this.loading = true;
            this.errorText = false;
            try {
                const ranges = await API.graph(`
                    query getAvailableEvents($calculatorUUID: UUID) {
                        me {
                            client {
                                eventsRange(calculatorUUID: $calculatorUUID) {
                                    from, to
                                }
                            } 
                        }
                    }
                `, {
                    calculatorUUID: this.calculatorUUID
                });
                
                const start = ranges.me.client.eventsRange.from;
                const end = ranges.me.client.eventsRange.to;

                this.setupRanges(moment(start), moment(end));
            } catch(e) {
                console.error('Error while initializing reports: ', e);
                this.errorText = 'Error while initializing reports.';
            }
            this.loading = false;
        },
    },

    components: {
        ReportsDisplay,
        LeadsDisplay,
        VDatePicker,
        VProgress
    }
}
</script>

