<template>
    <div class="flex-layout flex-column flex-center">
        <div class="card flex-1">
            <div class="overview-row">
                <lead-table :displaying="displayed"
                    :loading="loading"
                    :leads="leads" emptyText="No Leads"
                    @lead-clicked="onClickLead">
                </lead-table>
            </div>
        </div>
        <pagination 
            @page-change="onPageChanged" 
            :disabled="loading"
            :items="leadCount" :displayed="displayed"
            style="align-self: center;"></pagination>
        
        <v-modal v-if="!loading"
            :fill-small-screen="true"
            :active = "showLeadModal"
            @xclick="showLeadModal = false"
            @shadow-click="showLeadModal = false">

            <div class="modal-title" slot="header">Lead</div>

            <div class="content" slot="body">
                <div class="info-group">
                    <div class="info-name">Name</div>
                    <div class="info-value">{{currentLead.firstName}} {{currentLead.lastName}}</div>
                </div>
                <div class="info-group">
                    <div class="info-name">Email Address</div>
                    <div class="info-value">
                        <email-link :email="currentLead.emailAddress"></email-link>
                    </div>
                </div>
                <div class="info-group">
                    <div class="info-name">Phone Number</div>
                    <div class="info-value">
                        <phone-link :phone="currentLead.phoneNumber"></phone-link>
                    </div>
                    <!-- TODO: format phone numbers, either here or on the server, probably the server. -->
                </div>
                <div class="info-group">
                    <div class="info-name">Loan Type</div>
                    <div class="info-value"><span class="label primary">{{formatCalculatorType(currentLead.metadata.loanType)}}</span></div>
                </div>
                <div class="info-group">
                    <div class="info-name">Loan Amount</div>
                    <div class="info-value">{{fmtmoney(currentLead.metadata.loanAmount)}}</div>
                </div>
                <div class="info-group">
                    <div class="info-name">Down Payment</div>
                    <div class="info-value">{{fmtmoney(currentLead.metadata.downPayment)}}</div>
                </div>
                <div class="info-group">
                    <div class="info-name">Created</div>
                    <div class="info-value">{{formatDateTime(currentLead.created)}}</div>
                </div>
            </div>
        </v-modal>
    </div>
</template>

<script>
import LeadTable from './lead-table';
import EmailLink from './email-link';
import PhoneLink from './phone-link';
import Pagination from './pagination';
import VModal from './v-modal';
import * as API from '../../misc/api';
import { timeout } from '../../misc/util';
import { createToast } from '../../misc/toast';
import * as moment from 'moment';
import DateMixin from '../../mixins/DateMixin';
import CalculatorMixin from '../../mixins/CalculatorMixin';
import PhoneMixin from '../../mixins/PhoneMixin';
import { formatNumberWithCommas } from '@/misc/util';

export default {
    mixins: [DateMixin, CalculatorMixin, PhoneMixin],
    props: ['range', 'from', 'to', 'calculatorUUID'],
    props: {
        'range': { type: Object },
        'from': { type: [Object, String, Number] },
        'to': { type: [Object, String, Number] },
        'calculatorUUID': { type: String },
        'displayed': { type: Number, 'default' : 10 }
    },

    watch: {
        'range': 'loadLeadsDisplay',
        'from': 'loadLeadsDisplay',
        'to': 'loadLeadsDisplay',
        'calculatorUUID': 'loadLeadsDisplay',
        'displayed': 'loadLeadsDisplay'
    },

    data: () => ({
        loading: false,
        leadCount: 1,
        leads: [],

        showLeadModal: false,
        currentLead: { metadata: {} },
    }),

    async created() {
        this.loading = true;
        await this.loadLeadsDisplay();
    },

    methods: {
        onClickLead(lead) {
            this.currentLead = lead || { metadata: {} };
            if (!this.currentLead.metadata) { this.currentLead.metadata = {}; }
            this.showLeadModal = true;
        },

        fmtmoney(x) {
            if (typeof x === 'number' || typeof x === 'string') {
                return '$' + formatNumberWithCommas(x);
            } else {
                return '$0';
            }
        },

        isSingleDay(from, to) {
            if (from && to) {
                from = moment(from);
                to = moment(to);
                return from.isSame(to, 'day');
            } else {
                return true;
            }
        },

        onPageChanged({page, items, displayed}) {
            const offset = displayed * page;
            this.loadLeads(offset, displayed);
        },

        async loadLeadsDisplay() {
            await this.countLeads();
            await this.loadLeads(0, this.displayed);
        },

        async countLeads() {
            try {
                const rangeMode = this.range || this.from;
                if (rangeMode) {
                    console.assert(this.range ? this.range.from : this.from, 'from must be defined if this is in range mode.');
                    const from = this.range ? moment(this.range.from) : moment(this.from);
                    const to = this.range ? moment(this.range.to) : moment(this.to);
                    const singleDay = !to || this.isSingleDay(from, to);
                    const data = await API.graph(`
                    query countLeadsInRange($from: Date!, $to: Date, $calculatorUUID: UUID) {
                        me {
                            client {
                                leadCountInRange(from: $from, to: $to, calculatorUUID: $calculatorUUID)
                            }
                        }
                    }`, {
                        from: from.format(), 
                        to: singleDay ? null : to.format(),
                        calculatorUUID: this.calculatorUUID
                    });
                    this.leadCount = data.me.client.leadCountInRange;
                } else {
                    const data = await API.graph(`
                    query countLeadsInRange($calculatorUUID: UUID) {
                        me {
                            client {
                                leadCount(calculatorUUID: $calculatorUUID)
                            }
                        }
                    }`, {
                        calculatorUUID: this.calculatorUUID
                    });
                    this.leadCount = data.me.client.leadCount;
                }
            } catch(e) {
                console.error('There was an error counting the leads: ', e);
                
                createToast({
                    text: 'There was an error loading the leads.',
                    type: 'error'
                });
            }
        },

        async loadLeads(offset, count) {
            this.loading = true;
            try {
                const rangeMode = this.range || this.from;
                if (rangeMode) {
                    console.assert(this.range ? this.range.from : this.from, 'from must be defined if this is in range mode.');
                    const from = this.range ? moment(this.range.from) : moment(this.from);
                    const to = this.range ? moment(this.range.to) : moment(this.to);
                    const singleDay = !to || this.isSingleDay(from, to);
                    const data = await API.graph(`
                    query getLeadsInARange($offset: Int!, $count: Int!, $from: Date!, $to: Date, $calculatorUUID: UUID) {
                        me {
                            client {
                                leadsInRange(offset: $offset, count: $count, from: $from, to: $to, calculatorUUID: $calculatorUUID) {
                                    firstName
                                    lastName
                                    emailAddress
                                    phoneNumber
                                    created
                                    metadata
                                }
                            }
                        }
                    }`, {
                        count, offset,
                        from: from.format(),
                        to: singleDay ? null : to.format(),
                        calculatorUUID: this.calculatorUUID
                    });
                    this.leads = data.me.client.leadsInRange;
                } else {
                    const data = await API.graph(`
                    query getLeadsInARange($offset: Int!, $count: Int!, $calculatorUUID: UUID) {
                        me {
                            client {
                                leads(offset: $offset, count: $count, calculatorUUID: $calculatorUUID) {
                                    firstName
                                    lastName
                                    emailAddress
                                    phoneNumber
                                    metadata
                                }
                            }
                        }
                    }
                    `, {
                        count, offset,
                        calculatorUUID: this.calculatorUUID
                    });
                    this.leads = data.me.client.leads;
                }
            } catch(e) {
                console.error('There was an error getting the leads: ', e);
                
                createToast({
                    text: 'There was an error loading the leads.',
                    type: 'error'
                });
            }
            this.loading = false;
        }
    },

    components: {
        LeadTable,
        Pagination,
        VModal,
        EmailLink,
        PhoneLink
    }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.info-group {
    display: flex;
    flex-direction: column;

    &:not(:last-child) {
        margin-bottom: $layout-padding;
    }

    .info-name {
        display: block;
        font-size: 90%;
        color: $default-text-muted-color;
    }

    .info-value {
        display: block;
    }
}
</style>


