import Vue from 'vue';
import Router from 'vue-router';

import Dashboard from '@/components/Dashboard';
import Overview from '@/components/overview';
import Leads from '@/components/leads/leads';

import Calculators from '@/components/calculators/calculators';
import CalculatorsList from '@/components/calculators/calculators-list';
import CalculatorsEditor from '@/components/calculators/calculators-editor';
import CalculatorsViewer from '@/components/calculators/calculators-viewer';

import Settings from '@/components/settings/settings';
import SettingsProfile from '@/components/settings/settings-profile';
import SettingsOrg from '@/components/settings/settings-org';
import SettingsBilling from '@/components/settings/settings-billing';
import SettingsConnections from '@/components/settings/settings-connections';

import Reports from '@/components/reports/reports';
import About from '@/components/about/about';
import Login from '@/components/login';
import Register from '@/components/register';

Vue.use(Router)

export default new Router({
    routes: [
        { path: '/', name: 'index', redirect: '/login' },
        { path: '/login', name: 'login', component: Login },
        { path: '/register', name: 'register', component: Register  },

        {
            path: '/dash',
            name: 'dashboard',
            redirect: '/dash/overview',
            component: Dashboard,
            
            children: [
                {
                    path: 'overview',
                    component: Overview
                },

                {
                    path: 'leads',
                    component: Leads
                },

                {
                    path: 'calculators',
                    redirect: 'calculators/list',
                    component: Calculators,
                    children: [
                        { name: 'calculator-list', path: 'list', component: CalculatorsList },
                        { name: 'calculator-edit', path: 'edit', component: CalculatorsEditor },
                        { name: 'calculator-view', path: 'view', component: CalculatorsViewer },
                    ]
                },

                {
                    name: 'settings',
                    path: 'settings',
                    component: Settings,
                    redirect: 'settings/profile',
                    children: [
                        { name: 'settings-profile', path: 'profile', component: SettingsProfile },
                        { name: 'settings-org', path: 'org', component: SettingsOrg },
                        { name: 'settings-billing', path: 'billing', component: SettingsBilling },
                        { name: 'settings-connections', path: 'connections', component: SettingsConnections },
                    ]
                },

                { path: 'reports', component: Reports },
                { path: 'about', component: About }
            ]
        }
    ]
})



