<template>
    <div class="card">
        <div class="card-header">
            <div class="card-title flex-layout flex-row flex-center-cross">
                <div class="overview-label-dot" :style="dotLabelStyle"></div>
                <div>{{title}}</div>
            </div>
            <div v-if="subtitle" class="card-subtitle">{{subtitle}}</div>
        </div>
        <div class="card-body">
            <div class="overview-big-stat">{{stat}}</div>
            <div class="overview-small-stat">{{substat}}</div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        'title': { type: String },
        'subtitle': { type: String },
        'stat': { type: [String, Number] },
        'substat': { type: [String, Number] },
        'labelColor': { type: String }
    },

    computed: {
        dotLabelStyle() {
            return {
                'background-color': this.labelColor
            }
        }
    }
};
</script>

<style lang="scss">
@import '~@/assets/sass/variables';

.overview-big-stat {
    font-size: 1.5rem;
}

.overview-small-stat {
    color: $primary-color;
}

.overview-label-dot {
    width: 8px;
    height: 8px;
    border-radius: 8px;
    margin-right: 4px;
}
</style>


