<template lang="pug">
.report-display-container.container
    .row.flex-center(v-if="loading && !errorText"): .col-6
        v-progress(indeterminate)
    .row.flex-center(v-if="errorText")
        .label.error {{errorText}}
    .row(v-if="!errorText" :class="{rinvisible: loading}"): .col-12
        .overview-cards-container
            overview-card(title="Visits"
                subtitle="Visitors"
                :label-color="visitsColor"
                :stat="data.visitCount"
                class="flex-1")
            overview-card(title="Engagements"
                subtitle="Loan Estimates Calculated"
                :label-color="engagementsColor"
                :stat="data.engagementCount"
                :substat="engagementPercent"
                class="flex-1")
            overview-card(title="Conversions"
                subtitle="Applications Filled Out"
                :label-color="conversionsColor"
                :stat="data.conversionCount"
                :substat="conversionPercent"
                class="flex-1")
    .row(v-if="!errorText && data.labels && data.points && showGraph" :class="{rinvisible: loading}"): .col-12
        .canvas-resizer
            v-stacked-chart(
                :labels="data.labels"
                :datasets="data.points"
                :width="1600"
                :height="400"
            )
    .row(v-if="!loading && !errorText && data.labels && data.points && !showGraph")
        .col-12.flex-layout.flex-center
            span.label Cannot Display Graph For Ranges Greater Than {{maxGraphRange.text}}
</template>

<script>
import OverviewCard from './overview-card';
import VStackedChart from './v-stacked-chart';
import VProgress from './v-progress';
import VIcon from './v-icon';
import * as moment from 'moment';
import * as API from '@/misc/api';
import { hexAsRGBA } from '@/misc/util';

export default {
    props: {
        'from': { 'type': [Object, String, Number], },
        'to': { 'type': [Object, String, Number] },
        'range': { 'type': [Object] },
        'graph': { 'type': Boolean, 'default': true },
        'cards': { 'type': Boolean, 'default': true },
        'calculatorUUID': { 'type': String, 'default': null }
    },

    watch: {
        'from': 'fetchData',
        'to': 'fetchData',
        'range': 'fetchData',
        'calculatorUUID': 'fetchData'
    },

    created() {
        this.fetchData();        
    },

    data: () => ({
        loading: false,
        errorText: null,
        dataWaitingTag: 0,

        data: {
            labels: null,
            points: null,
            visitCount: 0,
            engagementCount: 0,
            conversionCount: 0,
        },

        visitsColor: '#fa5252',
        engagementsColor: '#329af0',
        conversionsColor: '#37b24d',

        maxGraphRange: {
            text: '100 Days',
            unit: 'days',
            count: 100
        },
    }),

    computed: {
        showGraph() {
            let from, to;

            if (this.range) {
                from = this.range.from;
                to = this.range.to;
            } else {
                from = this.from;
                to = this.to;
            }

            if (from && to) {
                return to.diff(from, this.maxGraphRange.unit) <= this.maxGraphRange.count;
            } else {
                return true;
            }
        },

        engagementPercent() {
            if (this.data.visitCount > 0) {
                return (this.data.engagementCount / this.data.visitCount * 100).toFixed(2) + '%';
            } else {
                return '0.00%';
            }
        },

        conversionPercent() {
            if (this.data.visitCount > 0) {
                return (this.data.conversionCount / this.data.visitCount * 100).toFixed(2) + '%';
            } else {
                return '0.00%';
            }
        }
    },

    methods: {
        isSingleDay(from, to) {
            if (from && to) {
                from = moment(from);
                to = moment(to);
                return from.isSame(to, 'day');
            } else {
                return true;
            }
        },

        useData(useHours, from, to, visits, engagements, conversions) {
            this.data.visitCount = this.countEventTotals(visits);
            this.data.engagementCount = this.countEventTotals(engagements);
            this.data.conversionCount = this.countEventTotals(conversions);

            if (!this.showGraph) return;
            
            // #TODO: change this to fix data that crosses date boundaries
            //        so I can support those.
            const tFormat = useHours ? 'LT' : 'DD/MM';

            // Sorts arrays by time and changes their time field to a formatted string.
            const fixArray = (arr) => {
                return arr.map(it => ({time: moment(it.time), count: it.count }))
                .sort((a, b) => a.time.diff(b.time))
                .map(it => ({time: it.time.format(tFormat), count: it.count }) )
            };

            const dv = visits.map(it => ({time: moment(it.time), count: it.count }))
                .sort((a, b) => a.time.diff(b.time));

            visits = visits ? fixArray(visits) : [];
            engagements = engagements ? fixArray(engagements) : [];
            conversions = conversions ? fixArray(conversions) : [];

            const labelledData = new Map(); // preserves insertion order

            // Add all hours/days in the range
            // This should help fill in gaps where there is no data.
            if (useHours) {
                from = from.clone();
                to = from.clone().endOf('day');

                while (from.isSameOrBefore(to)) {
                    labelledData.set(from.format(tFormat), [0, 0, 0]);
                    from.add(1, 'hour');
                }
            } else {
                from = from.clone();
                to = to.clone();

                while (from.isSameOrBefore(to)) {
                    labelledData.set(from.format(tFormat), [0, 0, 0]);
                    from.add(1, 'day');
                }
            }

            const addLabelledData = (out, darray, p) => {
                for (let d of darray) {
                    if (!out.has(d.time)) out.set(d.time, [0, 0, 0]);
                    out.get(d.time)[p] = d.count;
                }
            };
            addLabelledData(labelledData, visits, 0);
            addLabelledData(labelledData, engagements, 1);
            addLabelledData(labelledData, conversions, 2);

            const visitsData = [];
            const engagementsData = [];
            const conversionsData = [];
            const labels = [];
            for (let [key, values] of labelledData.entries()) {
                labels.push(key);
                visitsData.push(values[0]);
                engagementsData.push(values[1]);
                conversionsData.push(values[2]);
            }

            this.data.labels = labels;

            const visitsDataSet = {
                label: 'Visits',
                backgroundColor: hexAsRGBA(this.visitsColor, 1.0),
                fill: 'origin',
                data: visitsData
            };

            const engagementsDataSet = {
                label: 'Engagements',
                backgroundColor: hexAsRGBA(this.engagementsColor, 1.0),
                fill: 'origin',
                data: engagementsData
            };

            const conversionsDataSet = {
                label: 'Conversions',
                backgroundColor: hexAsRGBA(this.conversionsColor, 1.0),
                fill: 'origin',
                data: conversionsData
            };

            this.data.points = [conversionsDataSet, engagementsDataSet, visitsDataSet];
        },

        countEventTotals(events) {
            if (events) {
                return events.reduce((acc, cur) => acc + cur.count, 0);
            } else {
                return 0;
            }
        },

        async fetchData() {
            this.errorText = null;
            this.loading = true;
            const tag = ++this.dataWaitingTag;

            let data = null, singleDay = false, fFrom = null, fTo = null;
            let visits = null, engagements = null, conversions = null;
            try {
                let from, to;

                if (this.range) {
                    from = this.range.from;
                    to = this.range.to;
                } else {
                    from = this.from;
                    to = this.to;
                }

                const fetched = await this.__fetchDataFor(this.calculatorUUID, from, to);
                data = fetched.data;
                singleDay = fetched.singleDay;
                fFrom = fetched.from;
                fTo = fetched.to;

                visits = data.me.client.visits;
                engagements = data.me.client.engagements;
                conversions = data.me.client.conversions;
            } catch(e) {
                console.error('There was an error fetching report data: ', e);

                if (tag === this.dataWaitingTag) {
                    this.loading = false;
                    this.errorText = 'There was an error loading report data.';
                    return;
                }
            }

            if (tag === this.dataWaitingTag && data) {
                this.useData(singleDay, fFrom, fTo, visits, engagements, conversions);
                this.loading = false;
            }
        },

        async __fetchDataFor(calculatorUUID, from, to) {
            from = moment(from);
            if (to) to = moment(to);

            const singleDay = !to || this.isSingleDay(from, to);

            // console.log('fetch for %s — %s', from.format('LL'), to.format('LL'));
            const data = await API.graph(`
            query getEvents($from: Date!, $to: Date, $calculatorUUID: UUID) {
                me {
                    client {
                        visits(from: $from, to: $to, calculatorUUID: $calculatorUUID) { time, count }
                        engagements(from: $from, to: $to, calculatorUUID: $calculatorUUID) { time, count }
                        conversions(from: $from, to: $to, calculatorUUID: $calculatorUUID) { time, count }
                    }
                }
            }
            `, {
                from: from.format(),
                to: singleDay ? null : to.format(),
                calculatorUUID
            });

            return { singleDay, data, from, to };
        },
    },

    components: {
        OverviewCard,
        VStackedChart,
        VProgress
    }
}
</script>

<style lang="scss">
// This is the only way I know how to force chartjs to be the right size.
.canvas-resizer canvas {
    height: auto !important;
    width: 100% !important;
}
</style>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.report-display-container {
    .rinvisible {
        visibility: hidden;
    }

    .overview-cards-container {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        .card {
            margin-left: $layout-padding;
            margin-right: $layout-padding;
            margin-bottom: $layout-padding;
        }
    }
}
</style>



