<template lang="pug">
	input(ref="dateInput", :placeholder="placeholder || ''")
</template>

<script>
import moment from 'moment';

export default {
	name: 'date-picker',

	props: {
		value: [Object, Number, String],
        placeholder: String,
        utc: Boolean
	},

	data: () => ({
        date: null,
        fp: null
	}),

	watch: {
		value(newValue) {
            if (newValue) {
                const newDateValue = this.filterDateIn(moment(newValue));
                if (!this.date.isSame(newDateValue)) {
                    this.date = newDateValue;
                    this.fp.setDate(newDateValue.toDate());
                }
            } else {
                if (this.date) {
                    this.date = null;
                    this.fp.setDate(null);
                }
            }
		}
    },
    
    created() {
        if (this.value) this.date = this.filterDateIn(moment(this.value));
        else this.date = null;
    },

	mounted() {
        this.fp = new flatpickr(this.$refs.dateInput, {
            onChange: (selectedDates) => {
                this.date = moment(selectedDates[0]);
                this.$emit('input', this.filterDateOut(this.date));
            }
        });
        
        if (this.date) {
            this.fp.setDate(this.date.toDate());
        }
    },
    
    destroyed() {
        if (this.fp) {
            this.fp.destroy();
            this.fp = null;
        }
    },

	methods: {
        /**
         * Filters a date before it comes in.
         */
        filterDateIn(d) {
            return d;
        },

        /**
         * Makes the date a UTC date if necessary.
         */
        filterDateOut(d) {
            if (d && this.utc) return d.utc();
            else return d;
        }
	}
};
</script>


