<template lang="pug">
.settings-org-container
    .container
        .row.flex-center(v-if="loading && !errorText"): .col-6
            v-progress(indeterminate)
        .row.flex-center(v-if="errorText")
            .label.error {{errorText}}
        .row(v-if="!loading && !errorText"): .col-6.col-sm-12
            .form-group
                .form-label Branding Image
                div.flex-layout.flex-column.flex-center-cross
                    div: img.profile-img(src="" width=400 height=80 :src="client.brandingImageURL")
                    div: small.muted-text (Scaled to 400x80)
                    v-file-upload(:uploadTo="brandingImageUploadTo" @uploaded="uploadedBrandingImage")
                        | Change Branding Image
            .form-group
                .form-label Name
                input.form-input(v-model="client.name" type="text", placeholder="Organization Name")
            .form-group
                button.float-right.btn.btn-primary.btn-flex(@click="saveOrgInfo", :disabled="saving || !dirty")
                    <v-spinner icolor v-if="saving" style="margin-right: 8px;" size="1em"></v-spinner>
                    | Save
    
</template>

<script>
import VProgress from '../frag/v-progress';
import VSpinner from '../frag/v-spinner';
import VFileUpload from '../frag/v-file-upload';
import * as API from '../../misc/api';
import {createToast} from '../../misc/toast.js';
import Vue from 'vue';

export default {
    data: () => ({
        loading: true,
        saving: false,
        client: null,
        errorText: null,
        dirty: false,
    }),

    watch: {
        ['$route']() { this.fetchOrgInformation(); },

        client: {
            handler() { this.dirty = true; },
            deep: true
        }
    },

    computed: {
        brandingImageUploadTo() {
            return (process.env.SERVER_ORIGIN) + '/upload/branding';
        }
    },

    created() {
        this.fetchOrgInformation();
    },

    beforeRouteLeave(to, from, next) {
        if (this.dirty) {
            if (!confirm("You have unsaved changes. Are you sure you would like to nagivate away?")) {
                next(false);
                return;
            }
        }
        next();
    },

    methods: {
        uploadedBrandingImage(filename) {
            if (this.client) {
                this.client.brandingImageURL = (process.env.SERVER_ORIGIN) + "/ustatic/" + filename;
            }
        },

        async saveOrgInfo() {
            this.saving = true;
            try {
                const {updated} = await API.graph(`
                    mutation updateClientInfo($input: ClientInput!) {
                        updated: updateClient(clientInput: $input)
                    }
                `, { input: { ...this.client } });

                if (updated) {
                    createToast({
                        text: 'Updated organization information.',
                        type: 'success'
                    });
                } else {
                    createToast({
                        text: 'Clound not update organization information.',
                        type: 'error'
                    });
                }
            } catch(e) {
                console.error('Error while updating organization information.', e);
                createToast({
                    text: 'Error updating organization information',
                    type: 'error'
                });
            }
            this.saving = false;
            this.dirty = false;
        },

        async fetchOrgInformation() {
            this.loading = true;
            this.errorText = null;
            try {
                const {me} = await API.graph(`query {
                    me {
                        client {
                            brandingImageURL
                            name
                        }
                    }
                }`);

                //#TODO: make sure client exists maybe
                this.client = me.client;
            } catch(e) {
                console.error('Error while getting org information: ', e);
                this.errorText = 'There was an error getting your information.';
            }
            this.loading = false;
            Vue.nextTick(() => this.dirty = false);
        }
    },

    components: {
        VProgress,
        VFileUpload,
        VSpinner
    }
}
</script>

<style lang="scss" scoped>
@import "~@/assets/sass/variables";

.settings-org-container {
    padding: $layout-padding;
}
</style>



