export default {
    methods: {
        /**
         * Format's the calculator's type ID as more friendly text.
         * @param {string} typeId The identifier used for calculator's type.
         */
        formatCalculatorType(typeId) {
            switch (typeId) {
                case 'auto-loan': return 'Auto Loan';
                case 'mortgage-loan': return 'Mortgage';
                case 'personal-loan': return 'Personal Loan';
                default: return 'Unspecified';
            }
        }
    }  
};


