<template>
    <div class="toast-container">
        <div class="flex-1">&nbsp;</div>
        <transition name="toast-tran">
            <div v-show="toastVisible" class="toast" :class="toastClass">
                <button v-show="currentToast.canClose" @click="closeCurrentToast" class="btn btn-clear float-right"></button>
                {{currentToast.text}}
                <span v-if="currentToast.htmlText" v-html="currentToast.htmlText"></span>
            </div>
        </transition>
    </div>
</template>

<script>
import {registerToastHandler, unregisterToastHandler} from '../misc/toast';

export default {
    data: () => ({
        hasToast: false,
        toastVisible: false,

        lastToastClass: '',
        toastTransitionTimeout: null,
        toastEntranceDelay: 600,
        toastExitDelay: 600,
        toastHandler: null,
        toastQueue: [],

        currentToastPromise: null,
        currentToast: {
            text: 'Current Toast',
            htmlText: null,
            type: 'warning', // type: primary, default, error, success, warning
            canClose: false,
            delay: 600
        }
    }),

    created() {
        this.toastHandler = registerToastHandler((options) => {
            return this.submitToast(options);
        });
    },

    destroyed() {
        if (this.toastHandler) {
            unregisterToastHandler(this.toastHandler);
            this.toastHandler = null;
        }

        if (this.toastTransitionTimeout) {
            clearInterval(this.toastTransitionTimeout);
            this.toastTransitionTimeout = null;
        }
    },

    computed: {
        toastClass() {
            if (this.hasToast) {
                let clazz;
                switch (this.currentToast.type) {
                    case 'primary': clazz = 'primary'; break;
                    case 'error': clazz = 'error'; break;
                    case 'warning': clazz = 'warning'; break;
                    case 'success': clazz = 'success'; break;
                    default: clazz = 'default'; break;
                }
                this.lastToastClass = clazz;
                return clazz;
            } else {
                return this.lastToastClass;
            }
        }
    },

    methods: {
        submitToast(options) {
            return new Promise((resolve) => {
                const toastPromise = { resolve };
                const toast = Object.assign({
                    text: '!!!',
                    type: 'default',
                    canClose: true,
                    delay: 2000
                }, options);
                this.toastQueue.push({ toastPromise, toast });
                this.processNextToast();
            });
        },

        processNextToast() {
            if (this.hasToast) { return; }
            this.useNextToast();
        },

        useNextToast() {
            const toastInfo = this.toastQueue.shift();
            if (toastInfo && toastInfo['toast']) {
                this.currentToast = toastInfo['toast'];
                this.toastPromise = toastInfo['toastPromise'];
                this.hasToast = true;
                this.toastVisible = true;
                this.toastTransitionTimeout = setTimeout(() => {
                    this.toastDelay();
                }, this.toastEntranceDelay);
            }
        },

        toastDelay() {
            let delay;

            if (this.currentToast['delay'] === Infinity) {
                return; // Don't set the delay if it's infinite.
            }

            if (typeof this.currentToast['delay'] === 'string') {
                switch (this.currentToast.delay) {
                    case 'short': delay = 2000; break;
                    case 'long': delay = 4000; break;
                    default: delay = 2000; break;
                }
            } else {
                delay = parseInt(this.currentToast['delay']) || 2000;
            }

            this.toastTransitionTimeout = setTimeout(() => {
                this.onToastFinished();
            }, delay);
        },

        onToastFinished() {
            this.toastVisible = false;
            this.toastTransitionTimeout = setTimeout(() => {
                if (this.toastPromise) {
                    this.toastPromise.resolve();
                }
                this.hasToast = false;
                this.useNextToast();
            }, this.toastExitDelay);
        },

        closeCurrentToast() {
            if (this.hasToast) {
                if (this.toastTransitionTimeout) {
                    clearTimeout(this.toastTransitionTimeout);
                    this.toastTransitionTimeout = null;
                }
                this.onToastFinished();
            }
        }
    }
}
</script>

<style lang="scss">
.toast-tran-enter-active {
    animation: toast-entrance ease-in 0.5s;
}

.toast-tran-leave-active {
    animation: toast-exit ease-out 0.5s;
}


@keyframes toast-entrance {
    0% { opacity: 0; transform: translateY(100%); }
    60% { opacity: 0.6; transform: translateY(90%); }
    100% { opacity: 1.0; transform: translateY(0); }
}

@keyframes toast-exit {
    from { opacity: 1.0; transform: translateY(0); }
    to { opacity: 0; transform: translateY(100%); }
}
</style>

<style lang="scss" scoped>
.toast-container {
    display: flex;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;

    flex-direction: column;
    align-items: center;
    z-index: 999;
    padding: 16px;

    pointer-events:none;

    .toast {
        pointer-events: all;
    }
}
</style>



