<template lang="pug">
.mailchimp-conn-container
    v-loader(v-if="loading || errorText", :loading="loading", :errorText="errorText", loadingText="Loading MailChimp Info")
    template(v-else)
        div(v-if="mailchimpConnected")
            div(style="margin-bottom: 1em;")
                v-icon.success-color(icon="check-circle", style="margin-right: 1em;")
                | Connected to MailChimp as {{mailchimp.account.accountName}} 
                span.muted-text ({{mailchimp.account.username}})
            div.label Edit your calculators to connect them to your MailChimp email lists.
        div.flex-layout.flex-column.flex-align-start(v-else)
            button.btn.btn-mailchimp.btn-flex.flex-center-cross(style="height: auto;", 
                    @click="openMailChimpLogin")
                img(height="32", src="/sdash/static/img/mc_freddie_color.svg",
                    style="margin-top: 2px; margin-bottom: 2px; margin-right: 8px;")
                span Connect to MailChimp
</template>

<script>
import * as API from '../../../misc/api';
import VLoader from '../../frag/v-loader';
import VIcon from '../../frag/v-icon';
import {createToast} from '../../../misc/toast';

export default {
    props: ['client'],

    data: () => ({
        mailchimpConnected: false,
        errorText: null,
        loading: false,
        mailchimp: null,
    }),

    created() {
        this.onInitialized();
    },

    methods: {
        async onInitialized() {
            this.mailchimpConnected = false;
            this.loading = true;
            this.errorText = null;
            try {
                const data = await API.graph(`query getMailChimpInfo {
                    me {
                        client {
                            mailchimp {
                                connected
                                account {
                                    accountId, loginId, accountName
                                    email, firstName, lastName, username
                                    avatarURL
                                }

                                lists {
                                    id, webId, name
                                }
                            }
                        }
                    }
                }`);
                const connected = data.me.client.mailchimp.connected;
                if (connected) {
                    this.mailchimp = data.me.client.mailchimp;
                    this.mailchimpConnected = true;
                }
            } catch(e) {
                this.errorText = "There was an error while getting your MailChimp information."
                createToast({
                    text: 'Error while getting MailChimp information.',
                    type: 'error'
                });
                console.error('Error getting MailChimp information: ', e);
            }
            this.loading = false;
        },

        async openMailChimpLogin() {
            console.log('opening...');
            const data = await API.graph(`query getTempId{ me { client { tempId } } }`);
            const tempId = data.me.client.tempId;
            console.log('temp: ' + tempId);
            const origin = process.env.SERVER_ORIGIN;
            window.location.href = `${origin}/int/mailchimp/login/${tempId}`;
        }
    },

    components: {
        VLoader,
        VIcon
    }
}
</script>

<style lang="scss" scoped>
@import "~@/assets/sass/variables";
$mailchimp-color: lighten(#B7B7B7, 25%);

.success-color { color: $success-color; }

.mailchimp-conn-container {
    display: flex;
    flex-direction: column
}

.btn.btn-mailchimp {
    background-color: $mailchimp-color;
    color: $default-text-color;
    border: 1px solid darken($mailchimp-color, 5%);
    &:hover, &.selected {
        background-color: darken($mailchimp-color, 2%);
    }
}
</style>



