<template lang="pug">
.v-loader-container.flex-layout.flex-column
    div(v-if="loading && !errorText")
        div(v-if="loadingText", style="margin-bottom: 8px;") {{loadingText}}
        v-progress(style="max-width: 256px", indeterminate)
    div(v-if="errorText")
        div.label.error {{errorText}}
</template>

<script>
import VProgress from './v-progress';
export default {
    props: ['loading', 'loadingText', 'errorText'],
    components: {
        VProgress
    }
}
</script>

<style>

</style>



