import {generateFakeLeads, generateFakeData} from './api-test';
import http from './http';

const TOKEN_EXPIRATION_CORRECTION = 10000;

let __fakeData = undefined;
let __fakeLeads = undefined;

function getFakeData() {
    if (__fakeData === undefined) {
        __fakeData = generateFakeData(30);
    }
    return __fakeData;
}

function getFakeLeads() {
    if (__fakeLeads === undefined) {
        __fakeLeads = generateFakeLeads(100);
    }
    return __fakeLeads;
}

export const MONTHS = {
    JANUARY: 1, FEBRUARY: 2, MARCH: 3, APRIL: 4,
    MAY: 5, JUNE: 6, JULY: 7, AUGUST: 8, SEPTEMBER: 9,
    OCTOBER: 10, NOVEMBER: 11, DECEMBER: 12
};

// #TODO: this doesn't handle expiration yet.
let globalTokenInfo = null;
const api = http.api(process.env.SERVER_ORIGIN + '/api');
api.beforeSend = (request) => {
    if (globalTokenInfo && globalTokenInfo.token) {
        request.setRequestHeader('Authorization', 'Bearer ' + globalTokenInfo.token);
    }
};

// #TODO: this doesn't handle expiration yet.
// #TODO: this should also use localStorage.
export async function checkLoginStatus() {
    if (!globalTokenInfo) {
        const stored = localStorage['token-info'];
        if (stored) { globalTokenInfo = JSON.parse(stored); }
    }
    return globalTokenInfo && globalTokenInfo.token;
}

export async function clearLoginStatus() {
    globalTokenInfo = null;
    localStorage.removeItem('token-info');
}

export async function useTokenInfo(tokenInfo) {
    globalTokenInfo = tokenInfo;
    globalTokenInfo['expiresAt'] = Date.now() + (tokenInfo['expiresIn'] * 1000);
    if (tokenInfo) {
        localStorage['token-info'] = JSON.stringify(tokenInfo);
    }
}

export async function logout() {
    // #TODO: this should probably also send a message to the server or something
    //        so that I can invalidate the refresh token :P
    await clearLoginStatus();
}

export async function login(email, password) {
    return await api.post('/login').data({
        email, password
    }).json();
}

export async function signup(email, password, firstName, lastName, companyName) {
    return await api.post('/register').data({
        email, password, firstName, lastName, clientName: companyName
    }).json();
}

async function __ensureFresh(token) {
    if (!globalTokenInfo) {
        throw new Error("Can't refresh token null token.");
    }
    const {expiresAt, refresh: refreshToken} = globalTokenInfo;
    
    if (Date.now() > (expiresAt - TOKEN_EXPIRATION_CORRECTION)) {
        // expired
        await __refresh(refreshToken);
    }
}

async function __refresh(token) {
    console.log('Refreshing token...');

    const data = await api.post('/refresh').data({
        refreshToken: token
    }).json();

    if (data && !data['error']) {
        await useTokenInfo(data);
    } else {
        console.error('Error refreshing token.', data['message']);
    }
}

/**
 * Sends a graphQL query.
 * @param {string} query query to send.
 * @param {*} variables variables for query.
 * @param {string} operationName operation name of query.
 */
export async function graph(query, variables, operationName) {
    await __ensureFresh();
    
    const data = {query};
    if (variables) { data['variables'] = JSON.stringify(variables); }
    if (operationName) { data['operationName'] = operationName; }
    const result = await api.post('/graph').data(data).json();

    if (result['errors']) {
        console.error('GraphQL Error: ', result['errors']);
        throw new GraphError(result['errors']);
    } else if (!result.hasOwnProperty('data')) {
        throw new Error('No data property found on graph result.');
    }

    return result['data'];
}

/**
 * Retrieves a list of leads from the server.
 * @param {any} options Options for what leads should be retrieved and in what order.
 */
export async function getLeads(options) {
    options = Object.assign({
        offset: 0,
        count: 10,
        orderBy: 'created'
    }, options);

    // #TODO currently using fake data.
    return getFakeLeads().slice(options.offset, options.offset + options.count);
}

/**
 * Retrieves a list of points of visits, engagements, and conversions for a particular month.
 */
export async function getDataForMonth(options) {
    options = Object.assign({ year: 2017, month: MONTHS.JUNE }, options);

    // #TODO currently using fake data
    return getFakeData();
}

class GraphError extends Error {
    constructor(errors) {
        super('Graph Error');
        this.graphErrors = errors;
    }
}


