<template lang="pug">
.profile-settings-container
    .container
        .row.flex-center(v-if="loading && !errorText"): .col-6
            v-progress(indeterminate)
        .row.flex-center(v-if="errorText")
            .label.error {{errorText}}
        .row(v-if="!loading && !errorText"): .col-6.col-sm-12
            .form-group
                .form-label Profile Image
                div.flex-layout.flex-column.flex-center-cross
                    div: img.profile-img(src="" width=128 height=128 :src="profile.imageURL")
                    div: small.muted-text (Scaled to 128x128)
                    v-file-upload(:uploadTo="profileImageUploadTo" @uploaded="uploadedProfileImage")
                        | Change Profile Image
            .form-group
                label.form-label First Name
                input.form-input(v-model="profile.firstName" type="text", placeholder="First Name")
            .form-group
                label.form-label Last Name
                input.form-input(v-model="profile.lastName" type="text", placeholder="Last Name")
            .form-group
                button.float-right.btn.btn-primary(@click="saveProfileInfo", :disabled="saving || !dirty")
                    <v-spinner icolor v-if="saving" style="margin-right: 8px;" size="1em"></v-spinner>
                    | Save
</template>

<script>
import VProgress from '../frag/v-progress';
import VFileUpload from '../frag/v-file-upload';
import VSpinner from '../frag/v-spinner';
import VIcon from '../frag/v-icon';
import * as API from '../../misc/api';
import {createToast} from '../../misc/toast.js';
import Vue from 'vue';

export default {
    data: () => ({
        loading: false,
        errorText: null,
        saving: false,
        profile: null,
        dirty: false,
    }),

    watch: {
        ['$route']() {
            this.fetchProfileInformation();
        },

        profile: {
            handler() { this.dirty = true; },
            deep: true
        }
    },

    computed: {
        profileImageUploadTo() {
            return (process.env.SERVER_ORIGIN) + '/upload/profile';
        }
    },

    created() {
        this.fetchProfileInformation();
    },

    beforeRouteLeave(to, from, next) {
        if (this.dirty) {
            if (!confirm("You have unsaved changes. Are you sure you would like to nagivate away?")) {
                next(false);
                return;
            }
        }
        next();
    },

    methods: {
        uploadedProfileImage(filename) {
            if (this.profile) {
                this.profile.imageURL = (process.env.SERVER_ORIGIN) + "/ustatic/" + filename;
            }
        },

        async saveProfileInfo() {
            this.saving = true;
            try {
                const {updated} = await API.graph(`
                mutation updateProfileInfo($input: ProfileInput!) {
                    updated: updateProfile(profileInput: $input)
                }
                `, {
                    input: { ...this.profile } 
                });

                if (updated) {
                    createToast({
                        text: 'Updated profile information.',
                        type: 'success'
                    });
                } else {
                    createToast({
                        text: 'Could not update profile information.',
                        type: 'error'
                    });
                }
            } catch(e) {
                console.error("Error while updating profile information: ", e);
                createToast({
                    text: 'Error updating profile information.',
                    type: 'error'
                });
            }
            this.saving = false;
            this.dirty = false;
            this.$store.dispatch("loadProfileInfo");
        },

        async fetchProfileInformation() {
            this.loading = true;
            this.errorText = null;
            try {
                const {me} = await API.graph(`query {
                    me {
                        profile {
                            firstName
                            lastName
                            imageURL
                        }
                    }
                }`);

                //#TODO: make sure profile exists maybe
                this.profile = me.profile;
            } catch(e) {
                console.error('Error while getting profile information: ', e);
                this.errorText = 'There was an error getting your profile information.';
            }
            this.loading = false;
            Vue.nextTick(() => this.dirty = false);
        }
    },

    components: {
        VProgress,
        VFileUpload,
        VIcon,
        VSpinner
    }
}
</script>

<style lang="scss" scoped>
@import "~@/assets/sass/variables";

.profile-settings-container {
    padding: $layout-padding;
}
</style>



