<template>
<div class="login-main-container">
    <div class="card login-card">
        <div class="card-header">
            <div class="card-title">
                <v-branding style="align-items: flex-end;"><h5>Login</h5></v-branding>
            </div>
        </div>
        <div class="card-body">
            <form @submit="(evt) => {signup(evt); return false;}">
                <div class="form-group">
                    <label class="form-label" for="rinput-email">Email Address</label>
                    <input id="rinput-email" :disabled="signingUp" v-model="email" type="text" placeholder="Email Address" class="form-input">
                    <div class="form-error">{{errors.email}}</div>
                </div>

                <div class="form-group">
                    <label class="form-label" for="rinput-password">
                        Password <small>(at least 6 characters)</small>
                    </label>
                    <input id="rinput-password" :disabled="signingUp" v-model="password" type="password" placeholder="Password" class="form-input">
                    <div class="form-error">{{errors.password}}</div>
                </div>

                <div class="form-group">
                    <label class="form-label" for="rinput-password2">Re-Enter Password</label>
                    <input id="rinput-password2" :disabled="signingUp" v-model="passwordCheck" type="password" placeholder="Re-Enter Password" class="form-input">
                    <div class="form-error">{{errors.passwordCheck}}</div>
                </div>

                <div class="form-group">
                    <label class="form-label" for="rinput-firstname">First Name</label>
                    <input id="rinput-firstname" :disabled="signingUp" v-model="firstName" type="text" placeholder="First Name" class="form-input">
                    <div class="form-error">{{errors.firstName}}</div>
                </div>

                <div class="form-group">
                    <label class="form-label" for="rinput-lastname">Last Name</label>
                    <input id="rinput-lastname" :disabled="signingUp" v-model="lastName" type="text" placeholder="Last Name" class="form-input">
                    <div class="form-error">{{errors.lastName}}</div>
                </div>
                
                <div class="form-group">
                    <label class="form-label" for="rinput-orgname">Organization Name</label>
                    <input id="rinput-orgname" :disabled="signingUp" v-model="companyName" type="text" placeholder="Organization Name" class="form-input">
                    <div class="form-error">{{errors.companyName}}</div>
                </div>

                <div class="error-text">{{signupError || "&nbsp;"}}</div>

                <div class="form-group">
                    <div class="flex-layout flex-center-cross flex-align-right">
                        <button
                            type="button"
                            @click="gotoLogin"
                            :disabled="signingUp"
                            class="btn btn-link"
                            style="margin-right: 16px;">
                            Login
                        </button>
                        <button
                            :disabled="!signupButtonEnabled || signingUp"
                            @click="signup($event)" 
                            type="submit" 
                            class="btn btn-flex primary">
                            <v-spinner icolor class="login-spinner" v-if="signingUp" size="16px">
                            </v-spinner>
                            Sign Up
                        </button>
                    </div>
                </div>
            </form>
        </div>
    </div>
</div>
</template>

<script>
import VIcon from './frag/v-icon';
import VSpinner from './frag/v-spinner';
import VBranding from './frag/v-branding';
import * as API from '../misc/api';
import {mapState} from 'vuex';
import {createToast} from '../misc/toast';

export default {
    data: () => ({
        signupError: '',

        email: '',
        password: '',
        passwordCheck: '',
        firstName: '',
        lastName: '',
        companyName: '',

        errors: {
            email: '',
            password: '',
            passwordCheck: '',
            firstName: '',
            lastName: '',
            companyName: ''
        },

        signingUp: false
    }),

    mounted() {
    },

    methods: {
        gotoLogin() {
            this.$router.push('/login');
        },

        clearFormErrors() {
            this.errors.email = '';
            this.errors.password = '';
            this.errors.passwordCheck = '';
            this.errors.firstName = '';
            this.errors.lastName = '';
            this.errors.companyName = '';
        },

        containsFormErrors() {
            let errors = 0;
            this.clearFormErrors();

            if (!this.email.match(/.+@.+/)) {
                errors++;
                this.errors.email = 'Invalid email address.';
            }

            if (this.password != this.passwordCheck) {
                errors++;
                this.errors.passwordCheck = 'Does not match first password.';
            }

            return errors > 0;
        },

        async signup($event) {
            $event.preventDefault();
            this.signingUp = true;
            this.signupError = '';
            if (!this.containsFormErrors()) {
                try {
                    const signupResult = await API.signup(
                        this.email,
                        this.password,
                        this.firstName,
                        this.lastName,
                        this.companyName
                    );

                    if (signupResult && !signupResult['error']) {
                        createToast({
                            text: 'Successfully signed up for FinCalc',
                            type: 'success'
                        });
                        this.$router.replace('/login');
                    } else {
                        if (signupResult) {
                            this.signupError = signupResult['message'];
                        } else {
                            this.signupError = 'Unknown login error.';
                        }
                    }
                } catch(e) {
                    console.error('Error while signing up: ', e);
                    this.signupError = 'Unknown error while signing up.';
                }
            }
            this.signingUp = false;
        }
    },

    computed: {
        signupButtonEnabled() {
            return (
                (this.email && this.email.length >= 3) &&
                (this.password && this.password.length >= 6) &&
                (this.passwordCheck && this.passwordCheck.length >= 3) &&
                (this.firstName && this.firstName.length >= 1) &&
                (this.lastName && this.lastName.length >= 1) &&
                (this.companyName && this.companyName.length >= 1)
            );
        },

        ...mapState(['auth'])
    },

    components: {
        VIcon,
        VSpinner,
        VBranding
    }
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.login-main-container {
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: fixed;

    display: flex;
    align-items: center;
    justify-content: center;
}

.login-spinner {
    margin-right: 8px;
}

.login-card {
    min-width: 400px;
    max-height: 90%;
    overflow-y: auto;

    .error-text {
        color: $error-color;
        margin-top: 8px;
        margin-bottom: 8px;
    }
}

@media screen and (max-width: $screen-size-sm ) {
    .login-card {
        width: 100%;
        height: 100%;
        min-width: 100%;
        min-height: 100%;
        max-height: 100%;

        border: none;
    }
}
</style>



