<template>
    <div class="toolbar">
        <div class="toolbar-left flex-layout flex-center-cross"
            style="flex-direction: row; margin-left: 4px;">
            <button
                @click="toggleSidebarSmall"
                v-show="!displaySidebarSmall"
                class="btn btn-flex sidebar-toggle-btn">
                <v-icon icon="menu"></v-icon>
            </button>

            <v-branding class="toolbar-sm-logo"></v-branding>
        </div>
        <div class="flex-1">&nbsp;</div>
        <div class="dropdown dropdown-right flex-layout flex-center-cross flex-center" style="margin-right: 4px;" :class="{active: showProfileDropdown}">
            <a href="#"
                @blur="showProfileDropdown = false"
                @click.prevent="showProfileDropdown = !showProfileDropdown"
                class="toolbar-profile-link" tabindex="0">
                <span class="toolbar-user-info">
                    <avatar
                        class="toolbar-avatar"
                        :size="40"
                        :src="auth.profileImageURL">
                    </avatar>
                    <span class="toolbar-username">
                        {{profileFullName}}
                        <v-icon icon="chevron-down"></v-icon>
                    </span>
                </span>
            </a>

            <ul class="menu">
                <li class="menu-item">
                    <a href="#" @click.prevent="menuActionLogout">
                        <v-icon icon="log-out"></v-icon>
                        Log Out
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import VIcon from './frag/v-icon';
import VBranding from './frag/v-branding';
import Avatar from './frag/avatar';
import {graph, logout} from '../misc/api';
import {mapState, mapGetters, mapMutations} from 'vuex';

export default {
    data: () => ({
        showProfileDropdown: false
    }),

    computed: {
        ...mapGetters(['profileFullName']),
        ...mapState(['auth', 'displaySidebarSmall'])
    },

    components: {
        VIcon,
        Avatar,
        VBranding
    },

    methods: {
        async menuActionLogout() {
            await logout();
            this.$router.push('/login')
        },

        ...mapMutations(['toggleSidebarSmall'])
    }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';
@import '~@/assets/sass/mixins';

.toolbar {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    height: $toolbar-height;
    min-height: $toolbar-height;
    max-height: $toolbar-height;
    border-bottom: $border-width solid $border-color;
    // box-shadow: 0 4px 2px -2px rgba(0, 0, 0, $shadow-key-penumbra-opacity);
    z-index: $toolbar-z-index;
    background: transparent;

    align-items: stretch;

    a.toolbar-profile-link {
        color: inherit;
        outline: none;
        height: 100%;
        &:hover {
            color: $primary-color;
            text-decoration: none;
        }
    }

    .toolbar-user-info {
        display: inline-flex;
        flex-direction: row;
        align-items: center;
        margin-right: 16px;
        height: 100%;
        position: relative;
    }

    .toolbar-avatar {
        border: $border-width solid $border-color;
        margin-left: 8px;
        margin-right: 8px;
    }

    .toolbar-sm-logo {
        display: none;
    }

    .sidebar-toggle-btn {
        border: none;
        display: none;
        background: transparent;
        color: currentColor;
        font-size: $font-size-lg;
        text-align: center;
        min-width: 48px;

        &:hover {
            background: transparent;
            color: $primary-color;
        }

        &.active {
            color: $primary-color;
        }
    }

    @media screen and (max-width: $screen-size-sm ) {
        .sidebar-toggle-btn {
            display: inherit;
        }

        .toolbar-sm-logo {
            display: inherit;
        }
    }
}
</style>



