<template>
    <div class="pagination-container">
    	<ul class="pagination">
    		<li class="page-item">
    			<a href="#" :disabled="_disablePrevious" @click.stop.prevent="navtoPrev">
                    <v-icon icon="chevron-left"></v-icon>
                </a>
            </li>

    		<li :key="p" class="page-item" :class="{active: (_pageMin + p - 2) == page}" v-for="p in _pagesToShow">
                <a
                    href="#" 
                    @click.stop.prevent="navToPage(_pageMin + p - 2)" 
                    :disabled="disabled">
                    {{formatPage(_pageMin + p - 1)}}
                </a>
            </li>

    		<li class="page-item">
                <a
                    href="#"
                    :disabled="_disableNext"
                    @click.stop.prevent="navToNext">
                    <v-icon icon="chevron-right"></v-icon>
                </a>
            </li>
    	</ul>
    </div>
</template>

<script>
import VIcon from './v-icon';

export default {
    name: "pagination",
    props: {
        initPage: {
            type: Number,
            'default': 0,
            required: false
        },
        pagesDisplayed: {
            type: Number,
            'default': 11,
            required: false
        },
        numPages: {
            type: Number,
            'default': -1,
            required: false
        },
        displayed: {
            type: Number,
            'default': -1,
            required: false
        },
        items: {
            type: Number,
            'default': -1,
            required: false
        },
        disabled: {
            type: Boolean,
            'default': false,
            required: false
        }
    },
    data() {
        return {page: this.initPage};
    },
    watch: {
        page(p) {
            // console.log('Set To: ', p);
            return this.$emit('page-change', {
                page: p,
                items: this.items,
                displayed: this.displayed,
                numPages: this.numPages,
                pagesDisplayed: this.pagesDisplayed
            });
        }
    },
    computed: {
        _pagesToShow() {
            return Math.min(this._pageCount, this.pagesDisplayed);
        },
        _disablePrevious() { return this.disabled || (this.page <= 0); },
        _disableNext() { return this.disabled || (this.page >= (this._pageCount - 1)); },
        _pageMin() {
            if ((this._pageCount < 1) || (this._pageCount <= this.pagesDisplayed)) { return 1; }
            let halfdisplay = Math.floor(this.pagesDisplayed / 2);
            let min = Math.max(this.page - halfdisplay, 1);
            let max = Math.min(this.page + halfdisplay, this._pageCount - 1);
            if ((max - min) < this.pagesDisplayed) {
                min = Math.max((max - this.pagesDisplayed) + 2, 1);
            }
            return min;
        },
        _pageCount() {
            if (this.numPages > 0) {
                return this.numPages;
            } else if ((this.items > 0) && (this.displayed > 0)) {
                return Math.ceil(this.items / this.displayed);
            } else {
                return 0;
            }
        }
    },
    methods: {
        navToPage(p) {
            if (this.disabled) { return; }
            // console.log('nav to: ', p, this._pageCount);
            if ((p < this._pageCount) && (p >= 0)) {
                this.page = p;
                // console.log('page: ', this.page);
                // console.log('this: ', this);
                // setTimeout((() => console.log('-page: ', this.page)), 1);
                // return setTimeout((() => console.log('-this: ', this)), 1);
            }
        },
        navtoPrev(p) {
            if (this.disabled) { return; }
            return this.navToPage(this.page - 1);
        },
        navToNext(p) {
            if (this.disabled) { return; }
            return this.navToPage(this.page + 1);
        },
        formatPage(p) {
            let s = p.toString();
            if (s.length < 2) { return `0${s}`;
            } else { return s; }
        }
    },

    components: {
        VIcon
    }
}
</script>

<style lang="scss" scoped>
</style>


