import * as moment from 'moment';

export default {
    methods: {
        formatDateTime(date) {
            if (date === null) { return 'UNKNOWN'; }
            return moment(date).format('LLL');
        },

        formatDate(date) {
            if (date === null) { return 'UNKNOWN'; }
            return moment(date).format('LL');
        },

        formatMonth(date) {
            if (date === null) { return 'UNKNOWN'; }
            return moment(date).format('MMMM YYYY');
        }
    }
}


