<template>
  <span class="avatar" :style="computedAvatarStyle"></span>
</template>

<script>
export default {
    props: {
        'username': { type: String },
        'initials': { type: String },
        'src': { type: String },
        'bgColor': { type: String, 'default': '#ffffff' },
        'forceBG': { type:Boolean, 'default': false },
        'color': { type: String, 'default': 'inherit' },
        'size': { type: [String, Number], 'default': '32px' }
    },

    computed: {

        computedAvatarStyle() {
            let validSize;

            if (typeof this.size === 'number') {
                validSize = this.size + 'px';
            } else {
                validSize = this.size;
            }

            const _style = {
                'width' : validSize,
                'height': validSize,
                'border-radius': validSize
            };

            if (this.src) {
                if (this.forceBG) _style['background-color'] = this.bgColor;
                _style['background-image'] = `url(${this.src})`;
                _style['background-size'] = 'contain';
                _style['background-position'] = 'center';
            } else {
                _style['background-color'] = this.bgColor;
                _style['color'] = this.color;
            }

            return _style;
        }
    }
}
</script>

<style lang="scss" scoped>
.avatar {
}
</style>



