<template>
    <div class="dashboard-calculators-list">
        <div class="flex-layout flex-row flex-align-left">
            <h4>Calculators</h4>
            <div class="flex-1"></div>
            <button class="btn primary" @click="createNewCalculator">
                <v-icon icon="plus-square"></v-icon>
                Create Calculator
            </button>
        </div>
        <div class="card flex-1">
            <calculators-table :calculators="calculators" :loading="loading">
                <div slot="empty">
                    <div>
                        No Calculators Yet
                    </div>
                    <div>
                        <button
                            @click="createNewCalculator"
                            class="btn primary calc-table-btn-extra-margin">
                            <v-icon icon="plus-square"></v-icon>
                            Create Calculator
                        </button>
                    </div>
                </div>
            </calculators-table>
        </div>
    </div>
</template>

<script>
import CalculatorsTable from './calculators-table';
import * as API from '../../misc/api';
import VIcon from '../frag/v-icon';

export default {
    data: () => ({
        calculators: [],
        loading: false
    }),

    async mounted() {
        this.getCalculators(0, 10);
    },

    methods: {
        createNewCalculator() {
            this.$router.push('/dash/calculators/edit?mode=create');
        },

        async getCalculators(offset, count) {
            this.loading = true;
            const clist = await API.graph(`
                query listCalculators($offset:Int, $count:Int) {
                    list: listCalculators(offset:$offset, count:$count) {
                        name
                        shortid
                        uuid
                        calculatorType
                        modified
                    }
                }
            `, { offset, count });
            this.calculators = clist['list'];
            this.loading = false;
        }
    },

    components: {
        CalculatorsTable,
        VIcon
    }
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.dashboard-calculators-list {
    flex: 1;
    display: flex;
    padding: $layout-padding;
    align-items: stretch;
    flex-direction: column;
    overflow-y: auto;
}
</style>


