<template>
    <svg v-if="icon && icon.length" class="v-icon">
        <use class="v-icon-inner-use" :xlink:href="iconHref" />
    </svg>
</template>

<script>
export default {
    props: ["icon"],
    computed: {
        iconHref() {
            return '/sdash/static/img/feather.svg#' + this.icon;
        }
    }
}
</script>

<style lang="scss">
.v-icon {
    /* Flow with text content */
    display: inline-block;
    /* Inherit the parent text color */
    fill: currentColor;
    stroke: currentColor;
    /* Use the parent font-size for width and height */
    height: 1em;
    width: 1em;
    /* Vertically align icon with adjacent text */
    vertical-align: middle;
    /* Align more nicely with capital letters */
    position: relative;
    top: -0.0625em;
}

.v-icon-inner-use {
    fill: currentColor;
    stroke: currentColor;
}
</style>


