<template>
<table class="table table-hover responsive">
    <thead>
        <th>First Name</th>
        <th>Last Name</th>
        <th>Loan Type</th>
        <th>Loan Amount</th>
        <th>Down Payment</th>
        <th>Date Created</th>
    </thead>
    <tbody>
        <tr v-if="loading">
            <td colspan="6" class="empty-leads-text">
                <div style="flex-layout flex-center flex-center-cross">
                    <v-progress style="max-width: 256px;" indeterminate></v-progress>
                </div>
            </td>
        </tr>

        <tr v-if="!loading && (!leads || leads.length < 1)">
            <td colspan="6" class="empty-leads-text">
                <v-icon icon="users" />
                {{emptyText}}
            </td>
        </tr>

        <template v-if="!loading">
            <tr :key="lead" v-for="lead in leads" @click="onClickLead(lead)">
                <td data-th="First Name">{{lead.firstName}}</td>
                <td data-th="Last Name">{{lead.lastName}}</td>
                <td data-th="Loan Type"><span class="label primary">{{formatCalculatorType(lead.metadata.loanType)}}</span></td>
                <td data-th="Loan Amt.">{{fmtmoney(lead.metadata.loanAmount)}}</td>
                <td data-th="Down Payment">{{fmtmoney(lead.metadata.downPayment)}}</td>
                <td data-th="Created">{{formatDate(lead.created)}}</td>
            </tr>
        </template>

        <tr :key="row" v-for="row in remainingRows">
            <td style="visibility: hidden;" data-th="First Name">loading</td>
            <td style="visibility: hidden;" data-th="Last Name">loading</td>
            <td style="visibility: hidden;" data-th="Loan Type"><span class="label primary">loading</span></td>
            <td style="visibility: hidden;" data-th="Loan Amt.">loading</td>
            <td style="visibility: hidden;" data-th="Down Payment">loading</td>
            <td style="visibility: hidden;" data-th="Created">loading</td>
        </tr>
    </tbody>
</table>
</template>

<script>
import VIcon from './v-icon';
import VProgress from './v-progress';
import { formatNumberWithCommas } from '@/misc/util';
import CalculatorMixin from '../../mixins/CalculatorMixin';
import DateMixin from '../../mixins/DateMixin';

export default {
    mixins: [CalculatorMixin, DateMixin],

    props: {
        'leads': { type: Array, 'default': () => [] },
        'displaying': { type: Number, 'default': 10 },
        'loading': { type: Boolean, 'default': false },
        'emptyText': { type: String, 'default': "No Recent Leads" }
    },

    computed: {
        remainingRows() {
            if (!this.loading && this.leads && this.leads.length) {
                return this.displaying - this.leads.length;
            } else {
                if (!this.loading) { return this.displaying; }
                else { return Math.max(1, this.displaying - 1);  }
            }
        }
    },

    methods: {
        fmtmoney(x) { return '$' + formatNumberWithCommas(x); },

        onClickLead(lead) {
            this.$emit("lead-clicked", lead);
        }
    },

    components: {
        VIcon,
        VProgress
    }
};
</script>

<style lang="scss" scoped>
.empty-leads-text {
    padding: 16px;
    text-align: center;
    font-size: 1.5rem;
    vertical-align: middle;
}
</style>


