import Vue from 'vue';
import Vuex from 'vuex';

import BrandingModule from './modules/branding';
import AuthModule from './modules/auth';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        displaySidebarSmall: false
    },

    mutations: {
        showSidebarSmall: (state) => state.displaySidebarSmall = true,
        hideSidebarSmall: (state) => state.displaySidebarSmall = false,
        toggleSidebarSmall: (state) => state.displaySidebarSmall = !state.displaySidebarSmall
    },

    modules: {
        branding: BrandingModule,
        auth: AuthModule
    }
});

export default store;


