<template lang="pug">
span
    a(:href="phoneLink", v-if="typeof phone === 'string' && phone.length", target="_blank") {{formatPhoneNumber(phone)}}
    template(v-else) &lt;No Phone Number&gt;
</template>

<script>
// For any questions and customer service assistance please call <a href="tel:6176185557" target="_blank">(617) 618-5557</a>.
import PhoneMixin from "@/mixins/PhoneMixin";

export default {
    mixins: [PhoneMixin],
    props: ["phone"],
    computed: {
        phoneLink() {
            return this.formatPhoneLink(this.phone);
        }
    }
}
</script>

<style>

</style>



