<template>
    <div :class="{sidebar: true, 'sidebar-sm-visible': displaySidebarSmall}">
        <div class="sidebar-toolbar">
            <div class="toolbar-branding">
                <v-branding></v-branding>
                <span class="flex-1">&nbsp;</span>
                <button
                    @click="toggleSidebarSmall"
                    v-show="displaySidebarSmall"
                    class="btn btn-flex sidebar-toggle-btn">
                    <v-icon icon="menu"></v-icon>
                </button>
                <!--<v-icon class="toolbar-burger" icon="menu"></v-icon>-->
            </div>
        </div>
        <div class="sidebar-main">
            <div class="sidebar-list-header">Dashboard</div>
            <ul class="sidebar-item-list">
                <router-link :key="item.name" v-for="item in dashboardItems"
                    tag="li" class="sidebar-item" active-class="selected"
                    :to="item.to">
                    <span class="sidebar-item-icon">
                        <v-icon :icon="item.icon"></v-icon>
                    </span>
                    <span class="sidebar-item-text">{{item.name}}</span>
                </router-link>
            </ul>

            <div class="sidebar-list-header">More</div>
            <ul class="sidebar-item-list">
                <router-link :key="item.name" v-for="item in moreItems"
                    tag="li" class="sidebar-item" active-class="selected"
                    :to="item.to">
                    <span class="sidebar-item-icon">
                        <v-icon :icon="item.icon"></v-icon>
                    </span>
                    <span class="sidebar-item-text">{{item.name}}</span>
                </router-link>
            </ul>
        </div>
    </div>
</template>

<script>
import VIcon from './frag/v-icon';
import VBranding from './frag/v-branding';
import {mapState, mapMutations} from 'vuex';
export default {
    name: 'DashboardSidebar',

    data: () => ({
        dashboardItems: [
            {name: 'Overview', icon: 'activity', to: '/dash/overview'},
            {name: 'Leads', icon: 'user-check', to: '/dash/leads'},
            {name: 'Calculators', icon: 'percent', to: '/dash/calculators'},
            {name: 'Reports', icon: 'bar-chart-2', to: '/dash/reports'},
        ],
        moreItems: [
            {name: 'Settings', icon: 'settings', to: '/dash/settings'},
            {name: 'About', icon: 'info', to: '/dash/about'},
        ]
    }),

    computed: {
        ...mapState(['displaySidebarSmall'])
    },

    watch: {
        ['$route']() {
            this.hideSidebarSmall();
        }
    },

    methods: {
        openItem(item) {
            this.$router.push(item.to);
        },

        ...mapMutations(['toggleSidebarSmall', 'hideSidebarSmall'])
    },

    components: {
        VIcon,
        VBranding
    }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';
@import '~@/assets/sass/mixins';

.sidebar-list-header {
    padding: $layout-padding / 1.5;
    padding-left: $layout-padding;
    font-weight: bold;
    font-size: $font-size-sm;
    margin-top: 16px;
}

.sidebar-item-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: block;

    .sidebar-item {
        margin-top: 0;
        cursor: pointer;
        font-weight: 500;
        padding: $layout-padding / 1.5;
        padding-left: $layout-padding;
        border-left: 2px solid transparent;
        color: $default-text-color;
        display: block;
        // color: $default-text-muted-color;


        &.selected {
            background-color: $list-item-active-color;
            color: $primary-color;
            border-left: 2px solid $primary-color;
        }

        &:hover {
            // background-color: $list-item-hover-color;
            color: $primary-color;
        }

        .sidebar-item-text {
            margin-left: 8px;
        }
    }
}

.sidebar {
    width: $sidebar-width;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    border-right: 1px solid $border-color;
    // @include shadow-2dp;
}

.sidebar-toolbar {
    height: $toolbar-height;
    max-height: $toolbar-height;
    // border-bottom: $border-width solid $border-color;
    display: flex;
    display: row;
    padding: $toolbar-padding;
    width: $sidebar-width;

    // background-color: $primary-color;
    // color: $text-on-primary-color;
}

@media screen and (max-width: $screen-size-sm ) {
    .sidebar {
        width: 100%;
        position: fixed;
        left: 0;
        right: 0;
        background-color: $bg-color;
        display: none;
        z-index: $sidebar-z-index;
        @include shadow-2dp;

        &.sidebar-sm-visible {
            display: flex;
        }
    }

    .sidebar-toolbar {
        width: 100%;
    }
}

.toolbar-branding {
    font-size: px2rem(24);
    display: flex;
    flex-direction: row;
    align-items: center;
    flex: 1;

    .branding-img {

    }

    .toolbar-burger {
    }
}

.sidebar-main {
    flex: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}

.sidebar-toggle-btn {
    border: none;
    display: none;
    background: transparent;
    color: currentColor;
    font-size: $font-size-lg;
    text-align: center;
    min-width: 48px;

    &:hover {
        background: transparent;
        color: $primary-color;
    }

    &.active {
        color: $primary-color;
    }
}

@media screen and (max-width: $screen-size-sm ) {
    .sidebar-toggle-btn {
        display: inherit;
    }
}
</style>



