<template lang="pug">
.file-upload-container
    div.iu-loading.flex-layout.flex-column(v-if="loading")
        v-progress(indeterminate)
        span.center-text Uploading...
    div.iu-error-box.flex-layout.flex-column(v-if="!!errorText && !loading")
        span.center-text.label.error {{errorText}}
    input.iu-file-input(type="file" ref="fileInput" name="files[]" :id="fileUploadId" @change="fileChanged" :multiple="multiple")
    label.iu-file-label.btn(:for="fileUploadId", :class="{'iu-label-hide': loading}")
        v-icon(icon = "upload", :style="iconStyle")
        slot
</template>

<script>
import { getNewElementId, bytesAsFriendlyFileSize } from '../../misc/util';
import http from '../../misc/http';
import VIcon from './v-icon';
import VProgress from './v-progress';

export default {
    props: {
        "uploadTo": { type: String },
        "uploadField": { type: String, 'default': 'upload'  },
        "maxUploadSize": { type: Number, 'default': (1024 * 1024 * 2) },
        "multiple": { type: Boolean, 'default': false }
    },

    data: () => ({
        loading: false,
        errorText: null,
        fileUploadId: getNewElementId()
    }),

    computed: {
        iconStyle() {
            if (this.$slots['default']) {
                return "margin-right: 8px;";
            } else {
                return "";
            }
        }
    },

    methods: {
        async fileChanged() {
            this.loading = true;
            this.errorText = null;
            try {
                const inputFiles = this.$refs['fileInput'].files;
                const files = this.multiple ? inputFiles : inputFiles[0];

                if (files) {
                    let uploadSize = 0;
                    if (Array.isArray(files)) {
                        for (let file of files) { uploadSize += file.size; }
                    } else {
                        uploadSize = files.size;
                    }

                    if (uploadSize > this.maxUploadSize) {
                        this.errorText = `File is too large. (max: ${bytesAsFriendlyFileSize(this.maxUploadSize)})`;
                    } else {
                        const formData = new FormData();
                        formData.append(this.uploadField, files);
                        const response = await http.post(this.uploadTo)
                            .contentType(null)
                            .data(formData).json();

                        if (response && response['error']) {
                            this.errorText = response['message'];
                        } else {
                            const {filepath} = response;
                            this.$emit('uploaded', filepath);
                        }
                    }
                }
            } catch(e) {
                console.error("Error while uploadind file(s): ", e);
                this.errorText = "Error while uploading."
            }
            this.loading = false;
        }
    },

    components: {
        VIcon,
        VProgress
    }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.file-upload-container {
    display: flex;
    flex-direction: column;
    align-content: center;
    position: relative;
}

.iu-loading {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    padding: $layout-padding;
}

.iu-file-input {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.iu-label-hide {
    visibility: hidden;
    pointer-events: none;
}

.iu-error-box {
    margin-bottom: 4px;
}
</style>



