<template>
    <progress class="progress" :max="max" :value="realValue"></progress>
</template>

<script>
export default {
    props: ['indeterminate', 'value', 'max'],
    computed: {
        realValue() {
            if (this.indeterminate) return false;
            else return this.value;
        }
    }
}
</script>

<style lang="scss" scoped>
</style>



