import * as API from '../../misc/api';

export default {
    state: {
        authenticated: false,
        firstName: '',
        lastName: '',
        profileImageURL: '',
    },

    getters: {
        profileFullName(state) {
            if (state.firstName && state.lastName) {
                return state.firstName + ' ' + state.lastName;
            } else if (state.firstName) {
                return state.firstName;
            } else if (state.lastName) {
                return state.lastName;
            } else {
                return 'No Name';
            }
        }
    },

    mutations: {
        setProfileInfo(state, profile) {
            state.firstName = profile['firstName'];
            state.lastName = profile['lastName'];
            state.profileImageURL = profile['imageURL'];
        },

        setAuthenticated(state) {
            state.authenticated = true;
        }
    },

    actions: {
        async loadProfileInfo(self) {
            const profileInfo = await API.graph(`{
                me {
                    created
                    profile {
                        firstName
                        lastName
                        imageURL
                    }
                }
            }`);
            self.commit('setProfileInfo', profileInfo.me.profile);
        }
    }
};


