<template>
<div class="loan-term-editor">
    <div v-if="!terms || terms.length < 1"
        style="margin: 16px;"
        class="muted-text flex-layout flex-column flex-center-cross">
        <div style="font-size: 1.8rem;"><v-icon icon="list"></v-icon></div>
        <div>No Loan Terms Defines Defined</div>
    </div>

    <div :key="term" v-for="(term, index) in terms" class="loan-term-item">
        <button type="text" class="loan-term-delete-button" @click="removeTerm(index)">
            <v-icon icon="minus-circle" class="minus-button"></v-icon>
        </button>
        <div class="loan-term-item-inputs">
            <div class="form-line">
                <input 
                    :value="term.len"
                    @change="setTermItemNumberProp(term, 'len', $event)"
                    type="text" class="form-input" placeholder="Term">
                <select class="form-select" v-model="term.units">
                    <option value="months">Months</option>
                    <option value="years">Years</option>
                </select>
            </div>

            <div class="form-line">
                <input
                    :value="term.rate"
                    @change="setTermItemNumberProp(term, 'rate', $event)"
                    type="text" class="form-input" placeholder="Rate">
                <div class="form-static">%</div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import VIcon from '../frag/v-icon';

const NON_NUMERIC_MATCH = /[^0-9\.]+/g;

export default {
    model: { prop: 'terms', event: 'change' },

    props: {
        'terms': { type: Array, 'default': () => [] }
    },

    methods: {
        removeTerm(index) {
            // Not really efficient, but I never expect many items in this thing anyway :P
            const newTerms = this.terms.filter((_, idx) => idx !== index);
            this.$emit('change', newTerms);
        },

        setTermItemNumberProp(term, prop, $event) {
            const val = $event.target.value;
            NON_NUMERIC_MATCH.lastIndex = 0;
            const filteredVal = val.replace(NON_NUMERIC_MATCH, '');
            term[prop] = parseFloat(filteredVal) || 0;
        }
    },

    components: {
        VIcon
    }
}
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

.loan-term-editor {
    display: flex;
    flex-direction: column;
}

.minus-button {
    cursor: pointer;
    color: $oc-red-7;
    &:hover { color: $oc-red-4; }
}

.loan-term-delete-button {
    border: none;
    background: none;
    padding: 0;
    margin: 0;
}

.loan-term-item {
    display: flex;
    flex-direction: row;
    margin-bottom: 8px;
    align-items: center;

    .loan-term-delete-button {
        margin-right: 8px;
    }

    &-inputs {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;

        .form-line {
            &:not(:last-child) { margin-right: 4px; }
            margin-bottom: 4px;
        }

        .form-input {
            width: auto;
        }
    }
}
</style>



